import mapConfigTDT from './mapConfigTDT';
import mapConfigTDTOfMapbox from './mapConfigTDTOfMapbox';
import mapConfigBaidu from './mapConfigBaidu';

let mapConfig = mapConfigTDT;

if (constants.MAP_FRAME == 'mapbox' && constants.BASE_MAP == 'TDT') {
    mapConfig = mapConfigTDTOfMapbox;
} else if (constants.BASE_MAP == "Baidu") {
    mapConfig = mapConfigBaidu;
}

export default mapConfig;