/**
 * Map对象相关操作
 */

import 'mapbox-gl/dist/mapbox-gl.css'
import mapboxgl from 'mapbox-gl'

// 引入mapboxgl绘图控件样式文件
import '@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css'
import uuid from 'uuid';

import request from '@/plugin/axios'
import turfPointOnFeature from '@turf/point-on-feature';

// import {
//     Logo,
//     QueryByBoundsParameters,
//     QueryService
// } from '@supermap/iclient-mapboxgl';

class mapOper {

    /**
     * 构造函数
     * @param {*} mapConfig 地图初始配置
     */
    constructor(mapConfig, layerOper) {
        // 地图实例对象
        this.map = null;
        // 地图配置
        this.mapConfig = mapConfig;
        // 地图默认中心点
        this.defaultCenter = null;

        // 图层操作对象
        this.layerOper = layerOper;
        // 初始化图层操作
        this.layerApi = new this.layerOper(this.map);

        this.layers = [];

        // 地图单击事件集合
        this.mapClickEvents = [];
        // 地图鼠标移动事件
        this.mapMouseMoveEvents = [];
        // 地图比例缩放事件
        this.mapZoomEndEvents = [];
        // 鼠标当前移动位置经纬度
        this.mouseLocation = [0, 0];
        // 当前地图点击位置经纬度
        this.clickLocation = [0, 0];
        // 图层要素点击事件
        this.featureClickEvents = {}
        // 图层要素高亮事件
        this.featureHightEvents = {}
        // 鼠标高亮的要素
        this.hoveredFeature = null
        // 鼠标高亮的文本
        this.hoveredText = null
        this.activedText = null
        this.beforeActivedText = null
        // 鼠标选中激活的要素
        this.activedLayer = null
        this.activedFeature = null
        // 鼠标前一激活要素
        this.beforeActivedLayer = null
        this.beforeActivedFeature = null
    }

    /**
     * 初始化地图容器
     */
    initMap() {
        // 获取地图容器Div的id
        const domId = this.mapConfig.domId;
        // 获取地图预设中心点
        let mapCenter = [this.mapConfig.centerX, this.mapConfig.centerY];
        // 获取地图预设缩放级别
        const zoomLevel = this.mapConfig.zoomLevel;

        // 设置地图底图，设置数据源
        const sources = {};
        _.map(_.filter(this.mapConfig.initLayers, ['type', constants.BASE_MAP]), item => {
            sources['source_' + item.id] = {
                type: 'raster',
                tiles: [
                    item.url
                ],
                tileSize: 256
            }
        })

        // 设置基础图层，基础图层为天地图，在此过滤图层配置是天地图的
        const baselayers = _.map(_.filter(this.mapConfig.initLayers, ['type', constants.BASE_MAP]), item => {
            return {
                id: item.id,
                type: 'raster',
                minzoom: 0,
                maxzoom: 22,
                layout: {
                    visibility: item.isVisible ? 'visible' : 'none',
                },
                source: 'source_' + item.id,
                paint: {
                    // 'raster-saturation': 0.99,
                    // 'raster-hue-rotate': 170,
                    // 'raster-brightness-max':0.3
                    // 'raster-contrast': -0.8
                }
            }
        });

        // 初始化地图实例
        this.map = new mapboxgl.Map({
            container: domId,
            center: mapCenter,
            zoom: zoomLevel,
            bearing: this.mapConfig.bearing,
            pitch: this.mapConfig.pitch,
            style: {
                version: 8,
                glyphs: constants.MAPBOX_FONT_URL + "/{fontstack}/{range}.pbf",
                sources,
                layers: baselayers,
            },
        });

        // 初始化默认中心点
        this.defaultCenter = this.map.getCenter();

        // 初始化地图控件
        // this.initControls();

        // 初始化加载图层
        this.initLayers();
        // 初始化地图事件
        this.initEvents();
    }

    /**
     * 初始化地图图层
     */
    initLayers() {
        let that = this;
        let layers = [];

        for (let i = 0; i < this.mapConfig.initLayers.length; i++) {
            const single = this.mapConfig.initLayers[i]
            // 判断图层是否加载
            if (single.isLoad) {
                let layer = null;
                if (single.type == "WMTS") {
                    layer = this.layerApi.createWmtsLayer(single);
                } else if (single.type == "TDT") {
                    layer = this.layerApi.createTdtLayer(single);
                } else if (single.type == "Vector") {
                    layer = this.layerApi.createVectorLayer(single);
                } else if (single.type == "WMS") {
                    layer = this.layerApi.createWmsLayer(single);
                } else if (single.type == "WFS") {
                    layer = this.layerApi.createVectorLayerByWFS(single);
                } else if (single.type == "GeoJson") {
                    layer = this.layerApi.createGeoJsonLayer(single);
                } else if (single.type == "GeoJson_Line") {
                    layer = this.layerApi.createGeoJsonLineLayer(single);
                } else if (single.type == "GeoJson_Text") {
                    layer = this.layerApi.createGeoJsonTextLayer(single);
                } else if (single.type == "Baidu") {
                    layer = this.layerApi.createBaiduLayer(single);
                } else if (single.type == "SuperMapZXY") {
                    layer = this.layerApi.createSuperMapZxyLayer(single);
                } else if (single.type == "Extrusion") {
                    layer = this.layerApi.createExtrusionLayer(single, function (event) {
                        // 触发单击事件
                        that.featureClickEvents[event.layer.id].clickEvent({
                            features: [event.object]
                        })
                    }, single.isHoverLabel ? function (event) {
                        // 判断是否移动到图层要素上
                        if (event.object) {
                            // 设置鼠标样式
                            that.map.getCanvas().style.cursor = 'pointer';
                            let features = that.queryFeatureByCode(single.isHoverLabel, event.object.properties.CODE)
                            if (features.length > 0) {
                                that.setHoveredText(single.isHoverLabel, features[0].id)
                            }
                        } else {
                            that.map.getCanvas().style.cursor = '';
                            that.setHoveredText(single.isHoverLabel)
                        }
                    } : undefined)
                } else if (single.type == "ExtHight") {
                    layer = this.layerApi.createExtHightLayer(single);
                }

                layer && layers.push(layer);
            }
        }

        this.map.on('load', function () {
            layers = _.orderBy(layers, 'id');

            that.layers = layers;

            for (let i = 0; i < layers.length; i++) {
                that.map.addLayer(layers[i]);

                // 设置图层显隐
                if (layers[i].isVisible == false) {
                    that.map.setLayoutProperty(layers[i].id, 'visibility', 'none');
                }
            }

            // DeckGL图层加入后需要重新计算Canvas的大小
            that.map.resize()

            // 初始化标注图层
            that.initTextLayers();
            // 地图加载完成回调函数
            that.mapConfig.onLoadFunc()
        })

        return layers;
    }

    // 初始化标注图层
    initTextLayers() {
        let that = this
        // 设置标注数据源
        const textLayers = _.filter(this.mapConfig.initLayers, ['type', 'GeoJson_Text'])
        _.map(textLayers, item => {
            request({
                url: item.url,
                method: "get",
            }).then(data => {
                // 将区划信息存储到vuex中
                rootVm.$store.commit('techo/map/regionDataSet', {
                    code: item.name.split('_')[0],
                    data: data.features
                })

                data.features = _.map(data.features, feature => {
                    return {
                        type: feature.type,
                        id: feature.id,
                        properties: feature.properties,
                        geometry: turfPointOnFeature(feature.geometry).geometry
                    }
                })

                // 将中心点信息存储到vuex中
                rootVm.$store.commit('techo/map/centerDataSet', {
                    code: item.name.split('_')[0],
                    data: data.features
                })

                that.map.getSource(item.id).setData({
                    type: "FeatureCollection",
                    features: data.features
                })
            })
        })
    }

    /**
     * 初始化地图控件
     */
    initControls() {
        const Draw = new MapboxDraw({
            displayControlsDefault: false,
            controls: {
                line_string: true,
                trash: true
            }
        });

        this.map.addControl(Draw, 'top-left');
    }

    /** START 地图事件操作 */

    /**
     * 初始化地图事件
     */
    initEvents() {
        let that = this;

        // 地图单击事件
        this.map.on('click', function (...params) {
            that.clickLocation = arguments[0].lngLat.toArray()
            if (that.mapClickEvents.length > 0) {
                for (let i = 0; i < that.mapClickEvents.length; i++) {
                    that.mapClickEvents[i].value(...params, that.mapClickEvents[i].key)
                }
            }
        })

        // 地图鼠标移动事件
        this.map.on('mousemove', function (...params) {
            // 设置鼠标当前位置
            that.mouseLocation = arguments[0].lngLat.toArray();

            if (that.mapMouseMoveEvents.length > 0) {
                for (let i = 0; i < that.mapMouseMoveEvents.length; i++) {
                    that.mapMouseMoveEvents[i].value(...params, that.mapMouseMoveEvents[i].key)
                }
            }
        })

        // 地图显示比例事件
        this.map.on('zoomend', function (...params) {
            // 当前地图缩放比例
            const zoom = arguments[0].target.getZoom()

            if (that.mapZoomEndEvents.length > 0) {
                for (let i = 0; i < that.mapZoomEndEvents.length; i++) {
                    that.mapZoomEndEvents[i].value(zoom, that.mapZoomEndEvents[i].key)
                }
            }
        })
    }

    /**
     * 添加地图事件
     * @param {*} type 事件类型
     * @param {*} eventFunc 事件回调函数
     */
    addMapEvent(type, eventFunc) {
        const key = uuid.v1();
        const event = {
            key,
            value: eventFunc
        };

        switch (type) {
            case 'click':
                this.mapClickEvents.push(event);
                break;
            case 'mousemove':
                this.mapMouseMoveEvents.push(event);
                break;
            case 'zoomend':
                this.mapZoomEndEvents.push(event);
                break;
            default:
                break;
        }

        return key;
    }

    /**
     * 移除地图事件
     * @param {*} type 事件类型
     * @param {*} key 事件函数唯一标识码
     */
    removeMapEvent(type, key) {
        switch (type) {
            case 'click':
                _.remove(this.mapClickEvents, {
                    key,
                });
                break;
            case 'mousemove':
                _.remove(this.mapMouseMoveEvents, {
                    key,
                });
                break;
            case 'zoomend':
                _.remove(this.mapZoomEndEvents, {
                    key,
                });
                break;
            default:
                break;
        }

    }

    /**
     * 激活要素点击事件
     * @param {*} source 
     * @param {*} clickFunc 
     */
    activeFeatureClick(source, clickFunc) {
        let that = this

        if (this.featureClickEvents[source] == undefined) {
            this.featureClickEvents[source] = {}

            this.featureClickEvents[source].clickEvent = function (event) {
                let feature = event.features[0]

                // 判断鼠标是否位于Marker标签上
                if (!that.map.isOnMarker) {
                    clickFunc && clickFunc(feature, event.lngLat);
                }
            }

            this.map.on('click', source, this.featureClickEvents[source].clickEvent)
        }
    }

    /**
     * 反激活要素点击事件
     * @param {*} source 
     */
    deactiveFeatureClick(source) {
        if (this.featureClickEvents[source]) {
            this.map.off('click', source, this.featureHightEvents[source].clickEvent)

            this.featureHightEvents[source] = undefined
        }
    }

    /** END 地图事件操作 */

    /** START 图层高亮操作 */

    /**
     * 初始化图层高亮
     * @param {*} settings 
     */
    initHightLight(settings) {
        let that = this;

        _.map(settings, item => {
            // 绑定地图鼠标移动事件
            that.map.on('mousemove', item.origin, function (e) {

                // Change the cursor style as a UI indicator.
                // that.map.getCanvasContainer().style.cursor = "pointer"
                // that.map.getCanvas().style.cursor = 'pointer';

                let filter = e.features.reduce(function (memo, feature) {
                    memo.push(feature.properties.CODE);
                    return memo;
                }, ['==', 'CODE']);

                that.map.setFilter(item.hight, filter);
            });

            // 绑定地图鼠标离开事件
            that.map.on('mouseleave', item.origin, function (e) {
                that.map.setFilter(item.hight, ['==', 'CODE', '']);
            });
        })
    }

    /**
     * 初始化要素高亮
     * @param {*} source 
     */
    activeFeatureHight(source, mouseMoveFunc, mouseLeaveFunc) {
        let that = this

        if (this.featureHightEvents[source] == undefined) {
            this.featureHightEvents[source] = {}

            this.featureHightEvents[source].moveEvent = function (event) {
                // that.map.getCanvasContainer().style.cursor = "pointer"

                if (event.features.length > 0) {
                    if (that.hoveredFeature != null && that.activedFeature != that.hoveredFeature) {
                        that.map.setFeatureState({
                            source,
                            id: that.hoveredFeature
                        }, {
                            hover: false
                        });
                    }

                    that.hoveredFeature = event.features[0].id;
                    that.map.setFeatureState({
                        source,
                        id: that.hoveredFeature
                    }, {
                        hover: true
                    });
                }

                mouseMoveFunc && mouseMoveFunc();
            }

            this.map.on('mousemove', source, this.featureHightEvents[source].moveEvent)

            this.featureHightEvents[source].leaveEvent = function (event) {
                that.map.getCanvasContainer().style.cursor = ""

                if (that.hoveredFeature != null && that.activedFeature != that.hoveredFeature) {
                    that.map.setFeatureState({
                        source,
                        id: that.hoveredFeature
                    }, {
                        hover: false
                    });
                }
                that.hoveredFeature = null;

                mouseLeaveFunc && mouseLeaveFunc();
            }

            this.map.on('mouseleave', source, this.featureHightEvents[source].leaveEvent)
        }
    }

    /**
     * 注销要素高亮
     * @param {*} source 
     */
    deactiveFeatureHight(source) {
        if (this.featureHightEvents[source] && this.featureHightEvents[source].moveEvent) {
            this.map.off('mousemove', source, this.featureHightEvents[source].moveEvent)
        }

        if (this.featureHightEvents[source] && this.featureHightEvents[source].leaveEvent) {
            this.map.off('mouseleave', source, this.featureHightEvents[source].leaveEvent)
        }

        this.featureHightEvents[source] = undefined
    }

    /**
     * 设置标注高亮
     * @param {*} source 
     * @param {*} id 
     */
    setHoveredText(source, id) {
        if (id == undefined) {
            if (this.activedText != this.hoveredText) {
                this.map.setFeatureState({
                    source,
                    id: this.hoveredText
                }, {
                    hover: false
                });
            }
            this.hoveredText = null
            return
        } else {
            this.hoveredText = id;
            this.map.setFeatureState({
                source,
                id: this.hoveredText
            }, {
                hover: true
            });
        }
    }

    /**
     * 设置激活标注
     * @param {*} source 
     * @param {*} id 
     */
    setActivedText(source, id) {
        // 前一激活要素
        this.beforeActivedText = _.cloneDeep(this.activedText)

        // 取消前一激活多选
        if (this.beforeActivedText != null) {
            this.unsetActivedText(source, this.beforeActivedText);
            this.beforeActivedText = null
        }

        this.activedText = id

        this.map.setFeatureState({
            source,
            id,
        }, {
            hover: true
        });
    }

    /**
     * 取消激活标注
     * @param {*} source 
     * @param {*} id 
     */
    unsetActivedText(source, id) {
        if (source && id != undefined) {
            this.map.setFeatureState({
                source,
                id,
            }, {
                hover: false
            });
        } else {
            this.map.setFeatureState({
                source,
                id: this.activedText,
            }, {
                hover: false
            });
        }

        this.activedText = null
    }

    /**
     * 设置激活要素
     * @param {*} source 
     * @param {*} id 
     */
    setActivedFeature(source, id) {
        // 前一激活要素
        this.beforeActivedLayer = _.cloneDeep(this.activedLayer)
        this.beforeActivedFeature = _.cloneDeep(this.activedFeature)

        // 取消前一激活多选
        if (this.beforeActivedLayer != null) {
            this.unsetActivedFeature(this.beforeActivedLayer, this.beforeActivedFeature);
            this.beforeActivedLayer = null
            this.beforeActivedFeature = null
        }

        this.activedLayer = source
        this.activedFeature = id

        this.map.setFeatureState({
            source,
            id,
        }, {
            hover: true
        });
    }

    /**
     * 取消激活要素
     * @param {*} source 
     * @param {*} id 
     */
    unsetActivedFeature(source, id) {
        if (source && id != undefined) {
            this.map.setFeatureState({
                source,
                id,
            }, {
                hover: false
            });
            this.activedFeature = null
            this.activedLayer = null
        }
    }

    /** END 图层高亮操作 */

    /** START 网格图层样式初始化操作 */

    initLayerStyle(settings) {
        // that.map.addLayer();
    }

    /** END 网格图层样式初始化高亮操作 */

    queryFeatureByCode(source, code) {
        return this.map.querySourceFeatures(source, {
            filter: ["==", "CODE", code]
        })
    }

    /** START 地图缩放定位旋转操作 */

    // 获取当前地图缩放级别
    getZoom() {
        return this.map.getZoom();
    }

    // 获取当前地图最小缩放级别
    getMinZoom() {
        return this.map.getMinZoom();
    }

    // 获取当前地图最大缩放级别
    getMaxZoom() {
        return this.map.getMaxZoom();
    }

    // 地图放大一级
    zoomIn() {
        const zoom = this.getZoom();

        if (zoom != this.getMaxZoom()) {
            this.map.zoomTo(zoom + 1, {
                duration: 1000
            })
        }
    }

    // 地图缩小一级
    zoomOut() {
        const zoom = this.getZoom()
        if (this.getZoom() != this.getMinZoom()) {
            this.map.zoomTo(zoom - 1, {
                duration: 1000
            })
        }
    }

    // 缩放地图到指定级别
    setZoom(zoom) {
        this.map.zoomTo(zoom, {
            duration: 1000
        })
    }

    // 设置全图
    setFullExtent() {
        this.map.resetNorth().setPitch(0);
        this.setCenterAndZoom(this.defaultCenter, this.mapConfig.zoomLevel);

    }

    // 设置地图中心点及缩放级别
    setCenterAndZoom(center, zoom) {
        zoom = zoom ? zoom : this.map.getZoom();

        this.map.easeTo({
            center,
            zoom,
            duration: 1000,
            curve: 1,
            easing(t) {
                return t;
            }
        });
    }

    /** END 地图缩放操作 */

}

export default mapOper;