<template>
    <el-popover :width="width" :placement="placement" popper-class="position-map" trigger="hover" @after-enter="handleShow">
        <div :style="{ height: height + 'px' }">
            <vBaseMap ref="map" @onLoad="handleMapLoaded"></vBaseMap>
        </div>
        <el-button type="primary" size="small" slot="reference">地理坐标位置</el-button>
    </el-popover>
</template>

<script>
import turfCenterMass from "@turf/center-of-mass";
import request from "@/plugin/axios";
import vBaseMap from "@/map/base-map.vue";
export default {
  components: {
    vBaseMap
  },
  props: {
    width: {
      type: String,
      default: "400"
    },
    height: {
      type: String,
      default: "300"
    },
    eventType: {
      type: String,
      default: "point"
    },
    placement: {
      type: String,
      default: "bottom"
    },
    code: {
      type: String,
      default: "6102"
    }
  },
  data() {
    return {
      layerName: "",
      mapApi: null,
      layers: []
    };
  },

  mounted() {
    this.mapApi = this.$refs["map"].mapApi;
    this.layers = _.filter(this.mapApi.mapConfig.initLayers, item => {
      return item.id.indexOf("2") == 0 && item.isBase == true;
    });
  },

  watch: {
    eventType: {
      handler(value) {
        switch (value) {
          case "house":
            console.log("房子");
            break;
          case "grid":
            console.log("网格");
            break;

          default:
            break;
        }
      },
      immediate: true
    }
  },

  methods: {
    handleMapLoaded() {
      let that = this;
      _.map(this.layers, item => {
        this.mapApi.mapOper.activeFeatureClick(item.id, (feature, lngLat) => {
          if (that.code == feature.properties.CODE) {
            this.$emit("formLonLat", lngLat);
            that.addMarker(lngLat);
          } else {
            this.$popup.showNotification({
              type: "warning",
              message: "请选择点选对应区域！"
            });
          }
        });
      });

      // 设置楼栋单击事件
        this.mapApi.mapOper.activeFeatureClick("301", (feature, lngLat) => {
            that.$store.commit('techo/map/activedHouseLoad', feature.properties.CODE)
        })
    },

    handleShow() {
      this.$nextTick(() => {
        let codeLength = this.code.length;
        let zoom = 0;
        switch (codeLength) {
          case 6:
            this.layerName = "基础图层-区界";
            zoom = 9;
            break;
          case 9:
            this.layerName = "基础图层-街道办";
            zoom = 12;
            break;
          case 12:
            this.layerName = "基础图层-社区";
            zoom = 14;
            break;
          case 14:
            this.layerName = "基础图层-网格";
            zoom = 14.5;
            break;
          default:
            break;
        }
        if (this.code == constants.REGION.CODE) {
          this.layerName = "基础图层-市界";
          zoom = 8;
        }
        request({
          url: _.find(this.layers, ["name", this.layerName]).url,
          method: "get"
        }).then(res => {
          let feature = _.find(res.features, item => {
            return this.code == item.properties.CODE;
          });
          let lonLat = turfCenterMass(turfCenterMass(feature.geometry)).geometry
            .coordinates;
          this.mapApi.mapOper.setCenterAndZoom(lonLat, zoom);
        });

        this.currentLayer(this.layerName);
      });
    },

    // 当前显示图层
    currentLayer(nValue) {
      // 设置图层显隐
      _.map(this.layers, item => {
        if (item.name.indexOf(nValue) == 0) {
          this.mapApi.layerApi.setLayerVisibility(item.id, true);
        } else {
          this.mapApi.layerApi.setLayerVisibility(item.id, false);
        }
      });

      let nId = _.find(this.layers, ["name", nValue]).id;
      this.mapApi.mapOper.activeFeatureHight(nId);
    },

    addMarker(lonLat) {
      this.mapApi.markerApi.clearMarkers();
      this.mapApi.markerApi.addMarkers(
        [
          {
            type: "0101",
            lon: lonLat.lng,
            lat: lonLat.lat
          }
        ],
        {
          img_0101: require("./images/1.png")
        }
      );
      // 移动到标记点
      this.mapApi.mapOper.setCenterAndZoom([lonLat.lng, lonLat.lat]);
    },
  }
};
</script>

<style>
.position-map {
  padding: 0;
}
</style>
