<template>
    <div :id="['map_'+random]" class="map-wrapper" flex-box="1"></div>
</template>

<script>
    import uuid from 'uuid';
    import mapApi from "./index";

    export default {
        data() {
            return {
                random: uuid.v1(),
                mapApi: new mapApi(),
            };
        },
        mounted() {
            // 地图初始化
            this.mapApi.init(this.$el, this.handleMapLoaded);
        },
        methods: {
            handleMapLoaded() {
                this.$emit('onLoad')
            }
        }
    }
</script>

<style>
    .map-wrapper {
        width: 100%;
        height: 100%;
    }
</style>