import util from '@/libs/util'
import {
  accountLogout
} from '@/api/sys/account'

export default {
  namespaced: true,
  actions: {
    /**
     * @description 注销用户并返回登录页面
     * @param {Object} param context
     * @param {Object} param vm {Object} vue 实例
     * @param {Object} param confirm {Boolean} 是否需要确认
     */
    logout({
      commit
    }, {
      vm,
      confirm = false
    }) {
      // 判断是否需要确认
      if (confirm) {
        commit('techo/gray/set', true, {
          root: true
        })
        vm.$confirm('注销当前账户吗?', '确认操作', {
            confirmButtonText: '确定注销',
            cancelButtonText: '放弃',
            type: 'warning'
          })
          .then(() => {
            commit('techo/gray/set', false, {
              root: true
            })
            // 删除cookie
            util.cookies.remove('uuid')

            accountLogout();
          })
          .catch(() => {
            commit('techo/gray/set', false, {
              root: true
            })
            vm.$message('放弃注销用户')
          })
      } else {
        // 删除cookie
        util.cookies.remove('uuid')

        accountLogout();
      }
    },
    relogin({
      commit
    }, {
      vm
    }) {
      commit('techo/gray/set', true, {
        root: true
      })

      vm.$alert('当前登录已失效,请重新登录！', '确认操作', {
        confirmButtonText: '确定注销',
        type: 'warning',
        callback: action => {
          commit('techo/gray/set', false, {
            root: true
          })
          // 删除cookie
          util.cookies.remove('uuid')

          accountLogout();
        }
      })
    }
  },
  mutations: {
    /**
     * @description 用户登录后从持久化数据加载一系列的设置
     * @param {Object} state vuex state
     */
    load(state) {
      // DB -> store 加载用户名
      this.commit('techo/user/load')
      // DB -> store 加载主题
      this.commit('techo/theme/load')
      // DB -> store 加载页面过渡效果设置
      this.commit('techo/transition/load')
      // // DB -> store 持久化数据加载上次退出时的多页列表
      // this.commit('techo/page/openedLoad')
      // DB -> store 持久化数据加载这个用户之前设置的侧边栏折叠状态
      this.commit('techo/menu/asideCollapseLoad')
    }
  }
}