import {
    default as ol
} from './ol-index';

// 引入图层操作
import layerOper from './layerOper';
// 引入标记样式
import styles from './markerStyles';

class markerOper {
    /**
     * 构造函数
     * @param {*} map 
     */
    constructor(map) {
        this.map = map;

        // 初始化图层操作
        this.layerApi = new layerOper(this.map);
        // 默认图层名称
        this.defaultLayerName = "默认标记图层";
        // 默认标记图层
        this.defaultLayer = this.layerApi.getLayerByName(this.defaultLayerName);
        // 默认标记图层数据源
        this.defaultSource = this.defaultLayer.getSource();

        // 设置样式
        this.mStyles = styles;
    }

    /**
     * 初始化图层样式
     */
    initLyStyle() {
        let that = this;

        this.defaultLayer.setStyle((feature) => {
            return that.mStyles[feature.get('type')];
        });
    }

    /**
     * 添加标记到标记图层
     * @param {*} params 标记对象
     */
    addMarker(params) {
        const marker = new olFeature({
            type: params.type,
            taskNum: params.taskNum,
            geometry: new olPoint([params.lon, params.lat])
        });

        marker.setStyle([this.mStyles[marker.get('type')]]);

        this.defaultSource.addFeature(marker);

        return marker;
    }

    /**
     * 添加多个标记到标记图层
     * @param {*} markers 标记集合
     */
    addMarkers(markers) {
        let that = this;

        const features = _.map(markers, (item) => {
            return new olFeature({
                type: item.type,
                taskNum: item.taskNum,
                geometry: new olPoint([item.lon, item.lat])
            });
        })

        _.map(features, (item) => {
            item.setStyle([that.mStyles[item.get('type')]]);
        })

        this.defaultSource.addFeatures(features);
    }

    showOverlay(text) {
        let tooltipElement = document.createElement('div');
        tooltipElement.className = 'tooltip';
        tooltipElement.innerText = text;

        let tooltip = new ol.Overlay({
            element: tooltipElement,
            offset: [15, 0],
            positioning: 'center-left'
        });

        this.map.getOverlays().clear();
        this.map.addOverlay(tooltip);

        return tooltip;
    }

    clearOverlay() {
        this.map.getOverlays().clear();
    }

    /**
     * 根据标记编号移除标记
     * @param {*} id 标记编号
     */
    removeMarkerById(id) {
        let feature = _.find(this.defaultSource.getFeatures(), (item) => {
            return item.get('id') == id;
        })
        this.defaultSource.removeFeature(feature);
    }

    /**
     * 根据标记类型移除标记
     * @param {*} type 
     */
    removeMarkerByType(type) {
        let that = this;
        type = type ? type : 'redHot';

        let features = _.filter(this.defaultSource.getFeatures(), (item) => {
            return item.get('type') == type;
        })

        _.map(features, (item) => {
            that.defaultSource.removeFeature(item);
        })
    }

    /**
     * 根据指定的属性移除标记
     * @param {*} type 
     * @param {*} attrs 
     */
    removeFeatureByAttrs(attrs) {
        let that = this;
        let features = this.defaultSource.getFeatures();
        let removeFeatures = [];

        for (let i = 0; i < features.length; i++) {
            let isRemove = true;

            for (let j = 0; j < attrs.length; j++) {
                if (features[i].get(attrs[j][0]) != attrs[j][1]) {
                    isRemove = false;
                }
            }

            if (isRemove) {
                removeFeatures.push(features[i]);
            }
        }

        _.map(removeFeatures, (item) => {
            that.defaultSource.removeFeature(item);
        })
    }

    /**
     * 获取所有标记点
     */
    getMarkers() {
        return this.defaultSource.getFeatures();
    }

    /**
     * 清空标记图层上的所有元素
     * @param {*} layerName 
     */
    clearMarkers() {
        this.defaultLayer.getSource().clear();
    }
}

export default markerOper