/**
 * @desc 当前文件存储表单验证相关方法
 * @author Originaljoy
 */

const validate = {};

/**
 * 必填验证规则
 * @param {string} message
 * @param {string} type
 * @param {string} trigger
 */
validate.valiRequired = (message, trigger, type) => {
    return {
        required: true,
        message: message,
        trigger: trigger ? trigger : 'blur',
        type: type ? type : 'string'
    }
}

/**
 * 字符串最大长度验证规则
 * @param {double} max 最大长度
 */
validate.valiMaxLength = (max, trigger) => {
    return {
        max: max,
        validator: vLength,
        trigger: trigger ? trigger : 'blur'
    }
}

/**
 * 字符串长度验证 Validator
 * 说明：由于Js原生字符串长度判断将中文也当一个字符处理，
 *      而数据库中一个中文字符占两个字节；
 *      因此这里需要重新长度验证器。
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 */
const vLength = (rule, value, callback) => {
    if (value == undefined) {
        callback();
        return;
    }

    if (rule.max && !rule.min) {
        if (uLength(value) > rule.max) {
            callback(new Error('长度不能大于' + rule.max + ' 位字符!'));
        } else {
            callback();
        }
    } else if (!rule.max && rule.min) {
        if (uLength(value) < rule.min) {
            callback(new Error('长度不能小于' + rule.min + ' 位字符!'));
        } else {
            callback();
        }
    } else if (rule.max && rule.min) {
        if (uLength(value) < rule.min && uLength(value) > rule.max) {
            callback(new Error('长度应为' + rule.min + '到' + rule.max + ' 位字符!'));
        } else {
            callback();
        }
    }
}

/**
 * 整数验证规则
 * @param {*} trigger 
 */
validate.valiInteger = (trigger) => {
    return {
        validator: vInteger,
        trigger: trigger ? trigger : 'blur'
    }
}

/**
 * 整数验证 Validator
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
const vInteger = (rule, value, callback) => {
    if(!Number.isInteger(Number(value)) || String(value).indexOf(" ") != -1) {
        callback(new Error('请输入整数值并且不得包含空格！'))
    } else {
        callback()
    }
}

/**
 * 移动电话号码验证规则
 * @param {*} max 
 * @param {*} trigger 
 */
validate.valiTellPhone = (max, trigger) => {
    return {
        validator: vTellPhone,
        trigger: trigger ? trigger : 'blur'
    }
}

/**
 * 移动电话号码验证 Validator
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
const vTellPhone = (rule, value, callback) => {
    const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
    if (value && !reg.test(value)) {
        return callback(new Error('电话号码输入不正确！'));
    } else {
        callback();
    }
};

/**
 * 固定电话号码验证规则
 * @param {*} trigger 
 */
validate.tellPhone = (trigger) => {
    return {
        validator: vTel,
        trigger: trigger ? trigger : 'blur'
    }
}

/**
 * 移动电话号码验证 Validator
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
const vTel = (rule, value, callback) => {
    const reg = /0\d{10}/;
    if (value && !reg.test(value)) {
        return callback(new Error('电话号码输入不正确！'));
    } else {
        callback();
    }
};

/**
 * 固定电话号码和手机号码验证规则
 * @param {*} trigger 
 */
validate.tellAndPhone = (trigger) => {
    return {
        validator: vTelAndPhone,
        trigger: trigger ? trigger : 'blur'
    }
}

/**
 * 固定电话号码和手机号码 Validator
 * @param {*} rule 
 * @param {*} value 
 * @param {*} callback 
 */
const vTelAndPhone = (rule, value, callback) => {
    const reg = /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18([0-3]|[5-9]))|(17[0-7]))\d{8}$|^0\d{2,3}-?\d{7,8}$/;
    if (value && !reg.test(value)) {
        return callback(new Error('电话号码输入不正确！'));
    } else {
        callback();
    }
};

/**
 * 中文字符串长度计算
 * @param {any} text
 */
const uLength = (text) => {
    var len = 0;
    for (var i = 0; i < text.length; i++) {
        var code = text.charCodeAt(i);
        //单字节加1
        if ((code >= 0x0001 && code <= 0x007e) || (0xff60 <= code && code <= 0xff9f)) {
            len++;
        } else {
            len += 2;
        }
    }
    return len;
}

// 日期选择控件快捷按钮设置
validate.shortcutDateOptions = {
    shortcuts: [{
        text: '今天',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            // start.setTime(start.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', [start, end]);
        }
    }, {
        text: '最近一周',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
        }
    }, {
        text: '最近一个月',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
        }
    }, {
        text: '最近三个月',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
        }
    }]
};

// 身份证号码验证
validate.isIDCard = (trigger) => {
    return {
        validator: (rule, value, callback) => {
            if (value) {
              let res = isIdCardNo(value);
              if (!res) {
                return callback(new Error("身份证格式不正确"));
              } else {
                callback();
              }
            } else {
              callback();
            }
          },
        trigger: trigger ? trigger : 'blur'
    }
};

/**
 * 身份证号码验证
 * @param {any} idCard 身份证号码
 */
const isIdCardNo = (idCard) => {
    //15位和18位身份证号码的正则表达式
    var regIdCard = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/;
    //如果通过该验证，说明身份证格式正确，但准确性还需计算
    if (regIdCard.test(idCard)) {
        if (idCard.length == 18) {
            var idCardWi = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2); //将前17位加权因子保存在数组里
            var idCardY = new Array(1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2); //这是除以11后，可能产生的11位余数、验证码，也保存成数组
            var idCardWiSum = 0; //用来保存前17位各自乖以加权因子后的总和
            for (var i = 0; i < 17; i++) {
                idCardWiSum += idCard.substring(i, i + 1) * idCardWi[i];
            }
            var idCardMod = idCardWiSum % 11; //计算出校验码所在数组的位置
            var idCardLast = idCard.substring(17); //得到最后一位身份证号码

            //如果等于2，则说明校验码是10，身份证号码最后一位应该是X
            if (idCardMod == 2) {
                if (idCardLast == "X" || idCardLast == "x") {
                    //alert("恭喜通过验证啦！");
                    return true;
                } else {
                    // alert("身份证号码错误！");
                    return false;
                }
            } else {
                //用计算出的验证码与最后一位身份证号码匹配，如果一致，说明通过，否则是无效的身份证号码
                if (idCardLast == idCardY[idCardMod]) {
                    //alert("恭喜通过验证啦！");
                    return true;
                } else {
                    //alert("身份证号码错误！");
                    return false;
                }
            }
        }
    } else {
        //alert("身份证格式不正确!");
        return false;
    }
};



export default validate;