import MapboxDraw from '@mapbox/mapbox-gl-draw';
import mapboxgl from 'mapbox-gl'

import turfLength from '@turf/length';
import turfArea from '@turf/area';
import turfCentroid from '@turf/centroid';

class measureOper {

    /**
     * 构造函数
     * @param {*} map 操作地图对象
     */
    constructor(map) {
        this.map = map;

        // 是否正在绘制
        this.isSketch = false;
        // 当前量算类型 Line Area
        this.measureType = "";

        // 量算提示Div
        this.helpTooltip = null;
        this.helpTooltipEle = null;
        // 量算结果标识
        this.measureTooltip = null;
        this.measureTooltipEle = null;

        // 量算提示信息
        this.startDrawMsg = "在地图上单击，开始绘制。";
        this.continueMsg = "单击继续绘制，双击结束。";
    }

    /**
     * 激活距离量算
     */
    activeMeasureLength() {
        this.deactiveMeasure();
        // 设置量算类型
        this.measureType = "Line"
        // 创建绘图工具
        this.draw = this.createDraw();
        // 开启线段绘制
        this.draw.changeMode("draw_line_string");
        // 添加地图移动事件
        this.addMapMoveEvent();
        // 创建提示信息
        this.createHelptip();
        this.createMeasuretip();
        // 添加绘图事件
        this.addDrawEvent();
    }

    /**
     * 激活面积量算
     */
    activeMeasureArea() {
        this.deactiveMeasure();
        // 设置量算类型
        this.measureType = "Area"
        // 创建绘图工具
        this.draw = this.createDraw();
        // 开启线段绘制
        this.draw.changeMode("draw_polygon");
        // 添加地图移动事件
        this.addMapMoveEvent();
        // 创建提示信息
        this.createHelptip();
        this.createMeasuretip();
        // 添加绘图事件
        this.addDrawEvent();
    }

    /**
     * 取消地图量算
     */
    deactiveMeasure() {
        // 重置变量
        this.measureType = "";
        this.isSketch = false;
        // 清空测量结果
        this.draw && this.draw.trash();
        this.draw && this.draw.deleteAll();
        // 移除绘图交互
        this.draw && this.map.removeControl(this.draw);

        this.draw = null;
        // 移除提示
        this.helpTooltip && this.helpTooltip.remove();
        this.measureTooltip && this.measureTooltip.remove();
    }

    /**
     * 创建绘图交互
     */
    createDraw() {
        const draw = new MapboxDraw({
            displayControlsDefault: false,
            styles: [{
                    id: "gl-draw-line",
                    type: "line",
                    filter: ["all", ["==", "$type", "LineString"],
                        ["!=", "mode", "static"]
                    ],
                    layout: {
                        "line-cap": "round",
                        "line-join": "round"
                    },
                    paint: {
                        "line-color": "#ff813c",
                        "line-dasharray": [.2, 2],
                        "line-width": 2
                    }
                }, {
                    id: "gl-draw-polygon-fill",
                    type: "fill",
                    filter: ["all", ["==", "$type", "Polygon"],
                        ["!=", "mode", "static"]
                    ],
                    paint: {
                        "fill-color": "#ffdd67",
                        "fill-outline-color": "#ff813c",
                        "fill-opacity": 0.3
                    }
                },
                {
                    id: "gl-draw-polygon-stroke-active",
                    type: "line",
                    filter: ["all", ["==", "$type", "Polygon"],
                        ["!=", "mode", "static"]
                    ],
                    layout: {
                        "line-cap": "round",
                        "line-join": "round"
                    },
                    paint: {
                        "line-color": "#ff813c",
                        "line-dasharray": [.2, 2],
                        "line-width": 2
                    }
                }
            ]
        })
        this.map.addControl(draw)

        return draw
    }

    /**
     * 创建量算提示
     */
    createHelptip() {
        let helpTooltipEle = document.createElement('div');
        helpTooltipEle.className = 'map-tooltip map-tooltip-hidden';
        helpTooltipEle.innerHTML = this.startDrawMsg;

        this.helpTooltip = new mapboxgl.Marker({
            element: helpTooltipEle,
            offset: [90, 10]
        }).setLngLat([null, null]).addTo(this.map)

        this.helpTooltipEle = helpTooltipEle;
    }

    /**
     * 创建量测结果提示
     */
    createMeasuretip() {
        this.measureTooltipEle = document.createElement('div');
        this.measureTooltipEle.className = 'map-tooltip map-tooltip-measure';
        this.measureTooltip = new mapboxgl.Marker({
            element: this.measureTooltipEle,
            offset: [0, -25]
        }).setLngLat([null, null]).addTo(this.map)
    }

    /**
     * 添加地图移动事件
     */
    addMapMoveEvent() {
        let that = this;

        this.map.on('mousemove', mouseMoveFunc);

        function mouseMoveFunc(evt) {
            // 设置鼠标当前位置
            that.mouseLocation = evt.lngLat.toArray();

            let helpMsg = that.startDrawMsg;

            if (that.isSketch) {
                helpMsg = that.continueMsg;
            }

            that.helpTooltipEle.innerHTML = helpMsg;
            // 设置提示框位置       
            that.helpTooltip.setLngLat(evt.lngLat.toArray());

            that.helpTooltipEle.classList.remove('map-tooltip-hidden');
        }

        this.removeMapMoveEvent = () => {
            that.map.off('mousemove', mouseMoveFunc);
        }
    }



    /**
     * 添加绘图事件
     */
    addDrawEvent() {
        let that = this;

        // 创建绘图渲染事件
        this.map.on('draw.render', drawRenderFunc);

        function drawRenderFunc(evt) {
            const features = that.draw.getAll().features;

            if (features.length > 0) {
                const last = features[features.length - 1];

                let result;
                if (that.measureType == "Line" && last.geometry.coordinates.length > 1) {
                    // 设置绘制状态
                    that.isSketch = true
                    // 计算长度
                    result = turfLength(last, {
                        units: 'meters'
                    })
                    that.measureTooltipEle.innerHTML = result > 1e3 ? (result / 1e3).toFixed(2) + "公里" : result.toFixed(2) + "米";
                    that.measureTooltip.setLngLat(that.mouseLocation);
                } else if (that.measureType == "Area" && last.geometry.coordinates[0].length > 2) {
                    // 设置绘制状态
                    that.isSketch = true
                    result = turfArea(last)
                    that.measureTooltipEle.innerHTML = result > 1e4 ? (result / 1e4).toFixed(2) + "平方公里" : result.toFixed(2) + "平方米";
                    var center = turfCentroid(last).geometry.coordinates;
                    that.measureTooltip.setLngLat(center);
                }
            }
        }

        // 绘图模式修改后取消绑定绘图渲染事件
        this.map.once("draw.modechange", function (A) {
            // 取消绑定绘图渲染事件
            that.map.off("draw.render", drawRenderFunc);
            // 设置量算结束
            this.isSketch = false
            // 清除量算提示
            that.helpTooltip.remove();
            // 清除地图鼠标移动事件
            that.removeMapMoveEvent()
        })
    }


}

export default measureOper