  mounted() {
    this.lineChart = this.$echarts.init(this.$el);
    this.lineChart.setOption(this.lineOptions);
    this.$bus.$on("resizeChart", () => {
      this.lineChart.resize();
    });
    let that = this;
    const data = this.items.concat([this.item]);
    this.items.forEach(function(el) {
      if (el.active) {
        that.linesData.push({
          category: el.category,
          data: [
            {
              coord: el.value
            },
            {
              coord: [20, 200]
            }
          ]
        });
      }
    });
    const links = this.items.map(el => {
      return {
        //source: el.name,
        target: that.item.name,
        speed: el.speed,
        lineStyle: {
          normal: {
            color: el.speed ? "#12b5d0" : "#ffffff",
            curveness: 0.1
          }
        }
      };
    });
    // this.option.legend[0].data = this.categories.map(function(el) {
    //   return el.name;
    // });
    this.option.series[0].data = data;
    this.option.series[0].links = links;
    this.option.series[0].categories = this.categories;
    this.option.series[1].data = _.map(
      _.filter(this.linesData, item => {
        return item.category == 0;
      }),
      "data"
    );
    this.option.series[2].data = _.map(
      _.filter(this.linesData, item => {
        return item.category == 1;
      }),
      "data"
    );
    let myChart = this.$echarts.init(document.getElementById("myChartTwo"));
    myChart.setOption(this.option);
  },
