<template>

    <div class="box-wrap" :style="boxSize" :height="height" :width="width" :isHeader="isheader">
     <div class="horn">
        <div class="lt"></div>
        <div class="rt"></div>
        <div class="rb"></div>
        <div class="lb"></div>
     </div>
     <div class="box-wrap-header" v-if="isheader">
         <slot name="header"></slot>
     </div>
     <div class="box-wrap-content">
        <slot name="table"></slot>
        <slot name="card"></slot>
        <slot name="leftcard"></slot>
        <slot name="leftEcharts"></slot>
        <slot name="rEcharts"></slot>
        <slot name="myChartTwo"></slot>
        <slot name="time"></slot>
    </div>
  </div>

</template>

<script>
export default {
    props:{
        height:{
            type:String,
            default:"25%"
        },
        width:{
            type:String,
            default:"100%"
        },
        isheader:{
            type:Boolean,
            default:true
        }
    },
    data(){
        return{
            boxSize:{
                width:this.width,
                height:this.height
            }
        };
    },
    mounted(){

    }
};
