        > div {
            width: 10px;
            height: 10px;
            position: absolute;
        }

        .lt {
            border-top: 1px solid #00d3e7;
            border-left: 1px solid #00d3e7;
            left: -2px;
            top: -2px;
        }
        .rt {
            border-top: 1px solid #00d3e7;
            border-right: 1px solid #00d3e7;
            right: -2px;
            top: -2px;
        }
        .rb {
            border-bottom: 1px solid #00d3e7;
            border-right: 1px solid #00d3e7;
            right: -2px;
            bottom: -2px;
        }
        .lb {
            border-bottom: 1px solid #00d3e7;
            border-left: 1px solid #00d3e7;
            left: -2px;
            bottom: -2px;
        } 
    } 
    .box-wrap-header{
        color:#3DD9FF;
        height: 2em;
        line-height: 2.3em;
        padding-left: 0.8em;
        font-size:16px;
        letter-spacing: 3px;
        // font-weight: bold;
        // font-family: "Microsoft YaHei"
    } 
    .box-wrap-content {
