// 创建一个错误
function errorCreat(msg) {
    const err = new Error(msg)
    errorLog(err)
    // 删除异常抛出
    // throw err
}

// 记录和显示错误
function errorLog(err) {
    // 添加到日志
    store.dispatch('techo/log/add', {
        type: 'error',
        err,
        info: '数据请求异常'
    })
    // 打印到控制台
    if (process.env.NODE_ENV === 'development') {
        util.log.danger('>>>>>> Error >>>>>>')
        console.log(err)
    }

    // 显示提示
    util.popup.showErrorMessage({
        message: err.message
    })
}

export default service