import request from '@/plugin/axios'
import util from '@/libs/util.js'
import turfCenterMass from '@turf/center-of-mass'

export default {
    namespaced: true,
    state: {
        // 左侧面板激活
        activedLeft: '',
        // 右侧面板激活
        activedRight: '',
        // 当前激活的树节点
        activedNode: null,
        // 默认的区域信息
        defaultRegion: [],
        // 当前激活的楼栋信息
        activedHouse: null,
        // 当前选中的专题图层名称集合
        activedLayers: [],
        // 当前级别下显示地图图层
        currentLayer: "",
        // 区域中心点信息
        centerData: {},
        // 区域数据
        regionData: {},
        // 当前专题类型，可选值【theme_resource、theme_data】
        themeClass: 'theme_resource',
        // 数据专题面板查询数据类型
        queryDataType: '房屋信息',
        // 当前专题图层渲染类型
        themeType: "ICON",
        // 专题图层数据集合
        themeData: [],
        // 楼栋列表数据集合
        houseList: [],
        // 当前选中的房屋户信息
        activedHouseHolder: null
    },
    getters: {
        /**
         * @description 获取当前选中区域
         * @param {Object} state vuex state
         */
        currentRegion(state) {
            if (state.activedNode == null) {
                return {
                    code: constants.REGION.CODE,
                    name: constants.REGION.NAME
                }
            } else {
                let region = state.activedNode.data;
                region.level = state.activedNode.level;
                return region
            }
        }
    },
    mutations: {
        /**
         * @description 激活一个左侧面板
         * @param {Object} state vuex state
         * @param {String} name 需要激活的面板名称
         */
        activedLeftSet(state, name) {
            state.activedLeft = name
        },
        /**
         * @description 激活一个右侧面板
         * @param {Object} state vuex state
         * @param {String} name 需要激活的面板名称
         */
        activedRightSet(state, name) {
            state.activedRight = name
        },
        /**
         * @description 激活树节点
         * @param {Object} state vuex state
         * @param {String} node 需要激活节点，为null时取消激活
         */
        activedNodeSet(state, node) {
            state.activedNode = node
        },
        /**
         * @description 当前地图显示图层
         * @param {*} state 
         * @param {*} layer 
         */
        currentLayerSet(state, layer) {
            state.currentLayer = layer
        },
        centerDataSet(state, item) {
            state.centerData[item.code] = item.data
        },
        regionDataSet(state,item){
            state.regionData[item.code] = item.data
        },
        // 设置区域中心点数据、设置标注图层数据
        async centerDataLoad(state, item) {
            let center = await this.dispatch('techo/db/get', {
                dbName: 'sys',
                path: 'map.center' + item.code,
                defaultValue: [],
                user: true
            })

            // 后台请求区域数据并存储中心点为storge数据
            if (center.length == 0) {
                await request({
                    url: item.url,
                    method: "get",
                }).then(data => {
                    data.features = _.map(data.features, feature => {
                        return {
                            type: feature.type,
                            id: feature.id,
                            properties: feature.properties,
                            geometry_name: feature.geometry_name,
                            geometry: turfCenterMass(feature.geometry).geometry
                        }
                    })

                    this.dispatch('techo/db/set', {
                        dbName: 'sys',
                        path: 'map.center' + item.code,
                        value: {
                            type: "FeatureCollection",
                            data: data.features
                        },
                        user: true
                    })

                    mapApi.map.getSource(item.code).setData({
                        type: "FeatureCollection",
                        features: data.features
                    })
                })
            } else {
                mapApi.map.getSource(item.code).setData(center)
            }

            state.centerData[item.code] = center
        },
        /**
         * @description 加载默认区域
         * @param {Object} state vuex state
         */
        async defaultRegionLoad(state) {
            let region = await this.dispatch('techo/db/get', {
                dbName: 'sys',
                path: 'map.defaultRegion',
                defaultValue: [],
                user: true
            })

            // TODO 后期优化该代码
            if (region.length == 0) {
                await request({
                    url: "/grid/rstu/find/all/code?code=" + constants.REGION.CODE,
                    method: "get",
                }).then((res) => {

                    region = {
                        code: constants.REGION.CODE,
                        label: constants.REGION.NAME,
                        children: []
                    }

                    let itemONE = []
                    if (constants.REGION.CODE.length == 4) {
                        itemONE = _.orderBy(_.map(res.region, item => {
                            return {
                                code: item.code,
                                label: item.name,
                                children: []
                            }
                        }), 'code')

                        // 组织街道办区域
                        _.map(itemONE, item => {
                            item.children = _.orderBy(_.map(_.filter(res.street, street => {
                                return street.code.indexOf(item.code) >= 0
                            }), street => {
                                return {
                                    code: street.code,
                                    label: street.name,
                                    children: []
                                };
                            }), 'code')
                        })

                        // 组织社区区域
                        _.map(itemONE, county => {
                            _.map(county.children, item => {
                                item.children = _.orderBy(_.map(_.filter(res.community, community => {
                                    return community.code.indexOf(item.code) >= 0
                                }), community => {
                                    return {
                                        code: community.code,
                                        label: community.name,
                                        children: []
                                    };
                                }), 'code')
                            })
                        })

                        // 组织单元网格区域
                        _.map(itemONE, county => {
                            _.map(county.children, street => {
                                _.map(street.children, item => {
                                    item.children = _.orderBy(_.map(_.filter(res.unit, unit => {
                                        return unit.code.indexOf(item.code) >= 0
                                    }), unit => {
                                        return {
                                            code: unit.code,
                                            label: unit.name
                                        };
                                    }), 'code')
                                })
                            })
                        })
                    } else if (constants.REGION.CODE.length == 6) {
                        itemONE = _.orderBy(_.map(res.street, item => {
                            return {
                                code: item.code,
                                label: item.name,
                                children: []
                            };
                        }), 'code')

                        // 组织社区区域
                        _.map(itemONE, item => {
                            item.children = _.orderBy(_.map(_.filter(res.community, community => {
                                return community.code.indexOf(item.code) >= 0
                            }), community => {
                                return {
                                    code: community.code,
                                    label: community.name,
                                    children: []
                                };
                            }), 'code')
                        })

                        // 组织网格区域
                        _.map(itemONE, street => {
                            _.map(street.children, community => {
                                community.children = _.orderBy(_.map(_.filter(res.unit, unit => {
                                    return unit.code.indexOf(community.code) >= 0
                                }), unit => {
                                    return {
                                        code: unit.code,
                                        label: unit.name,
                                        children: []
                                    };
                                }), 'code')
                            })
                        })
                    } else if (constants.REGION.CODE.length == 9) {
                        itemONE = _.orderBy(_.map(res.community, item => {
                            return {
                                code: item.code,
                                label: item.name,
                                children: []
                            };
                        }), 'code')

                        // 组织网格
                        _.map(itemONE, community => {
                            community.children = _.orderBy(_.map(_.filter(res.unit, unit => {
                                return unit.code.indexOf(community.code) >= 0
                            }), unit => {
                                return {
                                    code: unit.code,
                                    label: unit.name,
                                    children: []
                                };
                            }), 'code')
                        })

                    } else if (constants.REGION.CODE.length == 12) {
                        itemONE = _.orderBy(_.map(res.unit, item => {
                            return {
                                code: item.code,
                                label: item.name,
                                children: []
                            };
                        }), 'code')
                    }

                    region.children = itemONE;
                })

                this.dispatch('techo/db/set', {
                    dbName: 'sys',
                    path: 'map.defaultRegion',
                    value: region,
                    user: true
                })
            }

            state.defaultRegion = [region];
        },
        /**
         * 加载房屋列表数据
         * @param {*} state 
         */
        async houseListLoad(state, params) {
            let disCode = state.activedNode.data.code;

            // 根据网格编号获取楼栋信息
            await request({
                url: "/compre/hp/buildinfo/findPage?page=" + params.page + "&pageSize=" + params.pageSize + "&districtCode=" + disCode + "&name=" + params.name,
                method: "get",
            }).then((res) => {
                state.houseList = res;
            })
        },
        /**
         * @description 激活楼栋信息
         * @param {Object} state vuex state
         * @param {*} house 需要激活的楼栋，为null时取消激活
         */
        activedHouseSet(state, house) {
            state.activedHouse = house
        },
        /**
         * @description 通过楼栋编号获取楼栋信息
         * @param {*} state 
         * @param {*} code 
         */
        async activedHouseLoad(state, code) {
            // 根据楼栋编号获取楼栋信息
            await request({
                url: "/compre/hp/buildinfo/findByBuildingCode?buildingCode=" + code,
                method: "get",
            }).then((res) => {
                state.activedHouse = res[0];
            })
        },
        /**
         * 添加选中的专题图层
         * @param {Object} state vuex state
         * @param {*} item 
         */
        addActivedLayer(state, item) {
            state.activedLayers.push(item)
        },
        /**
         * 移除选中的专题图层
         * @param {Object} state vuex state
         * @param {*} item 
         */
        removeActivedLayer(state, item) {
            const index = state.activedLayers.indexOf(item)
            state.activedLayers.splice(index, 1)
        },
        /**
         * 清空选中的专题图层
         * @param {*} state 
         */
        clearActivedLayer(state) {
            state.activedLayers = []
        },
        /**
         * 选中指定的房屋户信息
         * @param {*} state 
         * @param {*} item 
         */
        activedHouseHolderSet(state, item) {
            state.activedHouseHolder = item
        },
        /**
         * 设置当前专题图层渲染类型
         * @param {*} state 
         * @param {*} type 
         */
        themeTypeSet(state, type) {
            state.themeType = type
        },
        /**
         * 设置当前专题类型
         * @param {*} state 
         * @param {*} type 
         */
        themeClassSet(state, type) {
            state.themeClass = type
        },
        /**
         * 设置数据专题查询数据类型
         * @param {*} state 
         * @param {*} type 
         */
        queryDataTypeSet(state, type) {
            state.queryDataType = type
        }
    },
    actions: {
        /**
         * 初始化专题图层数据 
         */
        initThemeData({
            state,
            rootState
        }) {
            let aside = rootState.techo.menu.aside

            // 根据左侧菜单添加专题图层树
            aside.forEach((item, index) => {
                const node = {
                    code: 'pNode_' + index,
                    label: item.title,
                    children: [],
                    disabled: true
                }

                if (item.children) {
                    item.children.forEach(subitem => {
                        node.children.push({
                            code: subitem.path.split('/')[2],
                            label: subitem.title
                        })
                    })

                    state.themeData.push(node)
                }
            });
        }
    }
}