import Vue from 'vue'
import { HappyScroll } from 'vue-happy-scroll'
// 引入css
import 'vue-happy-scroll/docs/happy-scroll.css'

import techoContainer from './techo-container'
import techoList from './techo-list/techo-list.vue'
import techoDetails from './techo-details/techo-details.vue'
import basePeopleForm from './techo-people/techo-people.vue'
import techoPosition from './techo-position/map-position.vue'
import techoCard from './techo-card/Card.vue'
import techoIcon from './techo-icon/'

// 滚动条
Vue.component('happy-scroll', HappyScroll)
// 注意 有些组件使用异步加载会有影响
Vue.component('techo-container', techoContainer)
Vue.component('techo-list', techoList)
Vue.component('techo-details', techoDetails)
Vue.component('techo-people', basePeopleForm)
Vue.component('techo-position', techoPosition)
Vue.component('techo-card', techoCard)
Vue.component('techo-icon', techoIcon)
