/**
 * @desc 当前文件基于Moment库封装常用时间操作方法
 * @author Originaljoy
 */
import dayjs from 'dayjs'

const time = {
    dayjs,
}

/**
 * 日期格式化【默认格式:YYYY-MM-DD HH:ss:mm】
 * @param {*} value 
 */
time.formatDate = (value, type) => {
    value = value ? dayjs(value) : dayjs();
    type = type ? type : "YYYY-MM-DD HH:mm:ss";
    return value.format(type);
}

/**
 * 获取日期开始时间【日、月、年】
 * @param {*} value 
 * @param {*} type day、month、year
 */
time.formatSDate = (value, type) => {
    value = value ? dayjs(value) : dayjs();
    type = type ? type : 'day';
    return value.startOf(type).format("YYYY-MM-DD HH:mm:ss");
}


/**
 * 当日结束日期格式化
 * @param {*} value 
 */
time.formatEDate = (value) => {
    value = value ? moment(value) : moment();
    return value.endOf('day').format("YYYY-MM-DD HH:mm:ss");
}

/**
 * 获取当月月初日期
 * @param {*} value 
 */
time.getMonthSDate = (value) => {
    value = value ? moment(value) : moment();
    return value.startOf('month').startOf('day').format("YYYY-MM-DD HH:mm:ss");
}

/**
 * 获取日期结束时间【日、月、年】
 * @param {*} value 
 * @param {*} type day、month、year
 */
time.formatEDate = (value, type) => {
    value = value ? dayjs(value) : dayjs();
    type = type ? type : 'day';
    return value.endOf('day').format("YYYY-MM-DD HH:mm:ss");
}

