<template>
    <techo-container ref="container" type="full" >
        <div class="techo-tree-wrapper" v-show="showTree==true">
            <el-input placeholder="输入行政区关键字" size="small" v-model="filterText" clearable></el-input>
            <happy-scroll size="10" resize style="height: calc(100% - 42px); margin-top: 10px;">
                <el-tree ref="tree" class="dis-code" :data="disCodeOption" :default-expanded-keys="isShowCode" node-key="code" accordion :filter-node-method="filterNode" highlight-current
                @node-click="handleNodeClick" :expand-on-click-node="false"></el-tree>
            </happy-scroll>
        </div>
        <div class="techo-list-wrapper">
            <div v-if="$slots.header" class="techo-list__header" ref="header">
                <slot name="header" />
            </div>
            <div class="techo-list__body" ref="wrapper">
                <div class="techo-list__body-wrapper-inner">
                    <div class="techo-list__body-header">
                        <techo-icon class="techo-list__body-header-icon" name="list-ul" />
                        <span>{{title}}</span>
                        <div class="techo-list__body-header-tool">
                            <div class="techo-list__body-header-tool-item" @click="handleAdd">
                                <techo-icon name="plus" />
                                <span>增加</span>
                            </div>
                            <div class="techo-list__body-header-tool-item" @click="handleDeleteAll">
                                <techo-icon name="trash-o" />
                                <span>删除</span>
                            </div>
                        </div>
                    </div>
                    <div class="techo-list__body-table">
                        <el-table ref="table" size="medium" :data="data" border highlight-current-row tooltip-effect="dark"
                            stripe height="100%" style="width: 100%" @selection-change="handleSelectionChange" @row-click="handleRowClick"
                            header-cell-class-name="techo-list__body-table-header">
                            <el-table-column type="selection" width="40" align="center" :selectable="selectable"></el-table-column>
                            <el-table-column type="index" label="序号" width="50" align="center">
                            </el-table-column>
                            <el-table-column v-for="(value, key, index) in header" :key="index" :label="key" :prop="value"
                                align="center" show-overflow-tooltip></el-table-column>
                            <el-table-column label="操作" fixed="right" align="center" width="130">
                                <template slot-scope="scope">
                                    <el-button size="mini" class="techo-list__body-table-button-view" @click="handleView(scope.$index,scope.row)">查看</el-button>
                                    <el-button size="mini" class="techo-list__body-table-button-edit" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                </div>
            </div>
            <div class="techo-list__pager">
                <el-pagination ref="pager" @size-change="handlePagerChange" @current-change="handlePagerChange"
                    :page-sizes="pageSizes" layout="total, sizes, prev, pager, next, jumper" :total="recordCount">
                </el-pagination>
            </div>
        </div>
    </techo-container>
</template>

<script>
export default {
  props: {
    // 页面标题
    title: {
      type: String,
      required: true,
      default: "列表模板"
    },
    // 列表表头
    header: {
      type: Object
    },
    isShowCode: {
      type: Array,
      default: ["6102"]
    },
    // 列表绑定数据
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    // 总记录数
    recordCount: {
      type: Number,
      default: 0
    },
    // 每页记录数
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 30, 50, 100];
      }
    },
    disCodeOption: {
      type: Array,
      default() {
        return [];
      }
    },
    showTree: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      // 列表选中集合
      tableSelection: [],
      filterText: ""
    };
  },
  mounted() {},
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  methods: {
    /** 节点过滤 */
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    /** 节点被点击 */
    handleNodeClick(node) {
      this.$emit("onTreeNode", node);
    },
    // 获取分页信息
    getPageInfo() {
      let page, pageSize, pageInfo;

      // TODO 缓存分页信息
      if (this.$refs["pager"] == undefined) {
        page = 1;
        pageSize = 10;
      } else {
        page = this.$refs["pager"].internalCurrentPage;
        pageSize = this.$refs["pager"].internalPageSize;
      }

      return {
        page,
        pageSize
      };
    },
    // 分页控件改变触发事件
    handlePagerChange() {
      this.$emit("onPageChange", this.getPageInfo());
    },
    // 添加记录触发事件
    handleAdd() {
      this.$emit("onAdd");
    },
    // 删除多条记录触发事件
    handleDeleteAll() {
      let that = this;

      if (this.tableSelection.length > 0) {
        this.$popup.showConfirm({
          message: "你确定要删除选中记录吗?",
          successFunc: () => {
            that.$emit("onDelete", that.tableSelection);
          }
        });
      } else {
        this.$popup.showNotification({
          message: "没有选中的记录！"
        });
      }
    },
    /** 点击行选中 */
    handleRowClick(row) {
      if(!row.dicMap.isHaveSub){
        this.$refs.table.toggleRowSelection(row);
      }
    },
    // 查看记录详情
    handleView(index, data) {
      this.$emit("onView", index, data);
      event.stopPropagation();
    },
    // 编辑选中记录
    handleEdit(index, data) {
      this.$emit("onEdit", index, data);
      event.stopPropagation();
    },
    // 列表选择集改变
    handleSelectionChange(value) {
      this.tableSelection = value;
    },
    // 根据子项判断是否可选
    selectable(row, index) {
      if (row.dicMap.isHaveSub) {
        return false;
      } else {
        return true;
      }
    }
  }
};
</script>

<style lang="scss">
@import "./styles/techo-list.scss";
</style>