import Vue from 'vue'
import App from './App.vue'
// 1.3 引入babel和promise的polyfill使系统兼容IE浏览器11
import 'babel-polyfill'
require('es6-promise').polyfill()

// 2.2 引入全局状态管理器
import store from '@/store/index'

// 2.3 引入核心插件
import techo from '@/plugin/techo'
// 2.3 设置核心插件
Vue.use(techo)

// 2.4 菜单和路由设置
import router from '@/router/index'
import {
  menuHeader,
  menuAside
} from '@/menu'
import {
  frameInRoutes
} from '@/router/routes'

// 3.1 引入非父子组件间交互组件EventBus
import eventBus from '@/plugin/bus/Event_Bus.js'
Vue.prototype.$bus = eventBus

// 3.2 引入公用工具函数
import util from '@/libs/util.js'
window.util = util

// 3.2 引入echarts
import eCharts from 'echarts/lib/echarts.js';
Vue.prototype.$echarts = eCharts;

// 引入echarts组件
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/line';
import 'echarts/lib/chart/lines';
import 'echarts/lib/chart/pie';
import 'echarts/lib/chart/graph';
import 'echarts/lib/chart/tree';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/dataZoom';

// 3.4 设置jQuery 全局变量
import $ from 'jquery';
window.jQuery = window.$ = $;

// 初始化Vue
window.rootVm = new Vue({

  render: h => h(App),
 
}).$mount('#app')