<template>
  <div id="myChart" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
export default {
  data() {
    return {
      lineOptions: {},
      lineChart: null
    };
  },
  mounted() {
    this.drawLine();
    this.lineChart = this.$echarts.init(this.$el);
    this.lineChart.setOption(this.lineOptions);
    this.$bus.$on("resizeChart", () => {
      this.lineChart.resize();
    });
    // let that = this;
    // setTimeout(() => {
    //   let app = {
    //     currentIndex: -1
    //   };
    //   setInterval(function() {
    //     let dataLength = 6;
    //     // 取消之前高亮的图形
    //     that.lineChart.dispatchAction({
    //       type: "downplay",
    //       seriesIndex: 0,
    //       dataIndex: app.currentIndex
    //     });
    //     app.currentIndex = (app.currentIndex + 1) % dataLength;
    //     // 高亮当前图形
    //     that.lineChart.dispatchAction({
    //       type: "showTip",
    //       seriesIndex: 0,
    //       dataIndex: app.currentIndex
    //     });
    //   }, 1000);
    // }, 1000);
  },
  methods: {
    drawLine() {
      let lineChart = this.$echarts.init(document.getElementById("myChart"));
      lineChart.setOption({
        calculable: true,
        grid: {
          left: "10%",
          top: "15%",
          right: "5%",
          bottom: "6%",
          containLabel: true
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线
          }
        },
        xAxis: [
          {
            type: "category",
            data: [
              "市民政局",
              "市教育局",
              "市安监局",
              "市财政局",
              "市气象局",
              "市国土资源局",
              
            ],
            splitLine: { show: false }, //去除网格线
            max: 5,
            axisLabel: {
              color: "#5EA2AF",
              rotate: 25
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: "#02296e",
                width: 2
              }
            }
          }
        ],

        yAxis: [
          {
            type: "value",
            splitLine: { show: false }, //去除网格线
            axisLine: {
              lineStyle: {
                color: "#02296e",
                width: 2
              }
            },
            axisLabel: {
              color: "#5EA2AF"
            }
          }
        ],
        series: [
          {
            name: "资源数",
            type: "bar",
            barWidth: 15,
            barCateGoryGap: 50,
            data: [23, 1, 8, 18, 5, 6],
            clickable: true,
            itemStyle: {
              normal: {
                color: "#42A7FF"
              }
            }
          }
        ]
      });
    }
  }
};
</script>

