<template>
    <div class="md-card">
        <div class="md-card-toolbar" v-if="$slots.header || header">
            <div class="md-card-toolbar-actions">
                <slot name="actions">
                    <span v-if="upLevel" @click="handleUpLevel"><techo-icon class="techo-list__body-header-icon" title="上一级" name="mail-reply"/></span>
                    <techo-icon v-if="maxCard" class="techo-list__body-header-icon md-card-fullscreen-activate toolbar_fixed" title="最大化" name="arrows-alt"/>
                </slot>
            </div>
            <div class="md-card-toolbar-heading-text">
                <slot name="header">{{ header }}</slot>
            </div>
        </div>
        <div class="md-card-content" :style="bodyStyle">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import cardOper from "./cardOper.js";
import elementResizeEvent from "element-resize-event";

export default {
  name: "Common-Card",
  props: ["header", "bodyStyle", "resizeEvent", "upLevel", "maxCard"],
  mounted() {
    let that = this;
    // 设置卡片宽度和高度变化时响应事件
    elementResizeEvent(this.$el, function() {
      that.resizeEvent && that.$bus.$emit(that.resizeEvent);
    });

    cardOper.card_fullscreen({
      fullElement: that.$el.querySelector(".md-card-fullscreen-activate"),
      callback: () => {
        that.resizeEvent && that.$bus.$emit(that.resizeEvent);
      }
    });
  },

  methods: {
      handleUpLevel() {
          this.$emit("onUpLevel");
      }
  }
};
</script>

<style>
@import url("./card.css");
</style>