/**
 * @desc 当前文件根据原始DOM API封装常用DOM操作
 * @author Originaljoy
 */
const dom = {}

/**
 * 获取Dom元素的计算后样式
 * @param {*} el Dom元素
 */
dom.getCurrentStyle = (el) => {
    let style = null;

    // IE不支持getComputedStyle方法
    if (window.getComputedStyle) {
        style = window.getComputedStyle(el, null);
    } else {
        style = el.currentStyle;
    }

    return style;
}

/**
 * 获取Dom元素的计算后值样式，如height,width
 * @param {*} el 
 * @param {*} type 如height,width
 */
dom.getStyleValue = (el, type) => {
    return Number(dom.getCurrentStyle(el)[type].split("px")[0]);
}

export default dom;