import MapboxDraw from '@mapbox/mapbox-gl-draw';
import DrawRectangle from 'mapbox-gl-draw-rectangle-mode';
import mapboxgl from 'mapbox-gl'


class drawOper {

    /**
     * 构造函数
     * @param {*} map 操作地图对象
     */
    constructor(map) {
        this.map = map;

        // 绘图对象
        this.draw = null;
        // 当前绘图类型 Line Polygon Point
        this.drawType = "";
        // 当前绘制的对象
        this.geometry = null;

        this.isSketch = false;

        // 绘图模式添加矩形
        this.drawModes = MapboxDraw.modes;
        this.drawModes.draw_rectangle = DrawRectangle;

    }

    /**
     * 激活绘图控件，传入参数type和endFunc
     * type 可选值有【Point、Line、Polygon、Rectangle】
     * @param {*} params 
     */
    activeDraw(params) {
        // 激活前先销毁原有绘图
        this.deactiveDraw();

        this.draw = this.initDraw();
        this.drawType = params.type;

        this.isSketch = true;

        if (params.type == "Line") {
            // 开启线段绘制
            this.draw.changeMode("draw_line_string");
        } else if (params.type == "Point") {
            this.draw.changeMode("draw_point");
        } else if (params.type == "Polygon") {
            this.draw.changeMode("draw_polygon");
        } else if (params.type == "Rectangle") {
            this.draw.changeMode('draw_rectangle');
        }

        this.addDrawEvent(params.endFunc);

    }
    /**
     * 创建绘图交互
     */
    initDraw() {
        const draw = new MapboxDraw({
            displayControlsDefault: false,
            modes: this.drawModes,
            styles: [{
                    id: "gl-draw-line",
                    type: "line",
                    filter: ["all", ["==", "$type", "LineString"],
                        ["!=", "mode", "static"]
                    ],
                    layout: {
                        "line-cap": "round",
                        "line-join": "round"
                    },
                    paint: {
                        "line-color": "#ff813c",
                        "line-dasharray": [.2, 2],
                        "line-width": 2
                    }
                }, {
                    id: "gl-draw-polygon-fill",
                    type: "fill",
                    filter: ["all", ["==", "$type", "Polygon"],
                        ["!=", "mode", "static"]
                    ],
                    paint: {
                        "fill-color": "#ffdd67",
                        "fill-outline-color": "#ff813c",
                        "fill-opacity": 0.3
                    }
                },
                {
                    id: "gl-draw-polygon-stroke-active",
                    type: "line",
                    filter: ["all", ["==", "$type", "Polygon"],
                        ["!=", "mode", "static"]
                    ],
                    layout: {
                        "line-cap": "round",
                        "line-join": "round"
                    },
                    paint: {
                        "line-color": "#ff813c",
                        "line-dasharray": [.2, 2],
                        "line-width": 2
                    }
                }
            ]
        })
        this.map.addControl(draw)

        return draw
    }


    /**
     * 反激活绘图工具
     */
    deactiveDraw() {
        // 重置变量
        this.drawType = "";
        this.isSketch = false;
        // 清空测量结果
        this.draw && this.draw.trash();
        this.draw && this.draw.deleteAll();
        // 移除绘图交互
        this.draw && this.map.removeControl(this.draw);

        this.draw = null;
    }

    /**
     * 添加绘图事件
     */
    addDrawEvent(endFunc) {
        let that = this;

        // 绘图模式修改后取消绑定绘图渲染事件
        this.map.once("draw.modechange", function (A) {
            const features = that.draw.getAll().features;
            // 设置量算结束
            that.isSketch = false

            // 绘制完成几何图形
            let geo = features.length > 0 ? features[0] : null

            endFunc && endFunc(geo);
        })
    }
}

export default drawOper;