// 引入地图配置
import mapConfig from './config/';
// 引入地图操作
import mapOper from './openlayers/mapOper';
// 引入图层操作
import layerOper from './openlayers/layerOper';
import superOlLayerOper from './superOl/layerOper';
// 引入标记操作
import markerOper from './openlayers/markerOper';

/**
 * 地图操作接口
 */
class mapApi {

    /**
     * 构造函数
     */
    constructor() {
        this.$el = null;
    }

    /**
     * 初始化地图接口
     * @param {*} el 
     */
    init(el) {
        this.$el = el;

        this.layerApi = new layerOper(this.map);
        this.layerApi1 = new superOlLayerOper(this.map);

        // 设置地图容器Dom元素id
        mapConfig.domId = this.$el.id;

        // 初始化地图操作对象
        this.mapOper = new mapOper(mapConfig, layerOper);
        // 初始化地图
        this.mapOper.initMap();
        // 地图实例对象
        this.map = this.mapOper.map;

        // 初始化图层操作对象
        this.layerApi = new layerOper(this.map);
        // 初始化标记操作对象
        this.markerApi = new markerOper(this.map);
    }
}

export default new mapApi()