<template>
  <div class="container-component" ref="container">
    <!-- [card] 卡片容器 -->
    <techo-container-card v-if="type === 'card' && !betterScroll">
      <slot v-if="$slots.header" name="header" slot="header" />
      <slot />
      <slot v-if="$slots.footer" name="footer" slot="footer" />
    </techo-container-card>
    <!-- [card] 卡片容器 滚动优化 -->
    <techo-container-card-bs v-bind="$attrs" v-if="type === 'card' && betterScroll">
      <slot v-if="$slots.header" name="header" slot="header" />
      <slot />
      <slot v-if="$slots.footer" name="footer" slot="footer" />
    </techo-container-card-bs>
    <!-- [ghost] 隐形容器 -->
    <techo-container-ghost v-if="type === 'ghost' && !betterScroll">
      <slot v-if="$slots.header" name="header" slot="header" />
      <slot />
      <slot v-if="$slots.footer" name="footer" slot="footer" />
    </techo-container-ghost>
    <!-- [ghost] 隐形容器 滚动优化 -->
    <techo-container-ghost-bs v-bind="$attrs" v-if="type === 'ghost' && betterScroll">
      <slot v-if="$slots.header" name="header" slot="header" />
      <slot />
      <slot v-if="$slots.footer" name="footer" slot="footer" />
    </techo-container-ghost-bs>
    <!-- [container-full] 填充 -->
    <techo-container-full v-if="type === 'full' && !betterScroll">
      <slot v-if="$slots.header" name="header" slot="header" />
      <slot />
      <slot v-if="$slots.footer" name="footer" slot="footer" />
    </techo-container-full>
    <!-- [container-full-bs] 填充 滚动优化 -->
    <techo-container-full-bs v-bind="$attrs" v-if="type === 'full' && betterScroll" @onScroll="handlScroll">
      <slot v-if="$slots.header" name="header" slot="header" />
      <slot />
      <slot v-if="$slots.footer" name="footer" slot="footer" />
    </techo-container-full-bs>
  </div>
</template>

<script>
  // 组件
  import techoContainerFull from './components/techo-container-full.vue'
  import techoContainerFullBs from './components/techo-container-full-bs.vue'
  import techoContainerGhost from './components/techo-container-ghost.vue'
  import techoContainerGhostBs from './components/techo-container-ghost-bs.vue'
  import techoContainerCard from './components/techo-container-card.vue'
  import techoContainerCardBs from './components/techo-container-card-bs.vue'
  export default {
    name: 'techo-container',
    components: {
      'techo-container-full': techoContainerFull,
      'techo-container-full-bs': techoContainerFullBs,
      'techo-container-ghost': techoContainerGhost,
      'techo-container-ghost-bs': techoContainerGhostBs,
      'techo-container-card': techoContainerCard,
      'techo-container-card-bs': techoContainerCardBs
    },
    props: {
      // 容器样式
      type: {
        type: String,
        required: false,
        default: 'full'
      },
      // 滚动优化
      betterScroll: {
        type: Boolean,
        required: false,
        default: false
      }
    },
    methods: {
      handlScroll(args) {
        this.$emit('onScroll',args);
      }
    }
  }
</script>