<template>
    <div>
        <el-form ref="baseForm" :model="baseForm" :rules="teamRules" label-width="120px">
            <el-row :gutter="15">
                <el-col :span="6">
                    <el-form-item label="身份证号" prop="idCardNo">
                        <el-autocomplete class="inline-input"
                            v-model="baseForm.idCardNo"
                            :fetch-suggestions="querySearch"
                            placeholder="请填写身份证号"
                            @select="handleSelect">
                            <template slot-scope="{ item }">
                                <div style="display: inline-block;">{{ item.idCardNo }}</div>
                                <span style="float: right;">{{ item.name }}</span>
                            </template>
                        </el-autocomplete>
                    </el-form-item>
                    <el-form-item label="联系方式" prop="phone">
                        <el-input v-model="baseForm.phone" placeholder="请填写联系方式"></el-input>
                    </el-form-item>
                    <el-form-item label="政治面貌">
                        <el-select placeholder="请选择政治面貌" clearable v-model="baseForm.politicalStatus">
                            <el-option v-for="(item, index) in dic['政治面貌']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="籍贯">
                        <el-select placeholder="请选择籍贯" clearable v-model="baseForm.nativePlace">
                            <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>  
                </el-col>
                <el-col :span="6">
                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="baseForm.name" placeholder="请填写姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="曾用名">
                        <el-input v-model="baseForm.oldName" :maxlength="12" placeholder="请填写曾用名"></el-input>
                    </el-form-item>
                    <el-form-item label="民族">
                        <el-select placeholder="请选择民族" clearable v-model="baseForm.nation">
                            <el-option v-for="(item, index) in dic['民族']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item> 
                    <el-form-item label="宗教信仰">
                        <el-select placeholder="请选择宗教信仰" clearable v-model="baseForm.religiousBelief">
                            <el-option v-for="(item, index) in dic['宗教信仰']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="性别" prop="sex">
                        <el-select placeholder="请选择性别" clearable v-model="baseForm.sex">
                            <el-option v-for="(item, index) in dic['性别']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="出生日期">
                        <el-date-picker v-model="baseForm.birthDate" type="date" disabled placeholder="请选择日期"></el-date-picker>
                    </el-form-item>  
                    <el-form-item label="学历">
                        <el-select placeholder="请选择学历" clearable v-model="baseForm.education">
                            <el-option v-for="(item, index) in dic['学历']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="婚姻状况">
                        <el-select placeholder="请选择婚姻状况" clearable v-model="baseForm.marriage">
                            <el-option v-for="(item, index) in dic['婚姻状况']" :label="item.name" :value="item.code" :key="index"></el-option>
                        </el-select>
                    </el-form-item>  
                </el-col>
                <el-col :span="6" style="text-align: center;">
                    <el-upload class="avatar-uploader"
                        :action="sctionUrl"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="photoUrl" :src="photoUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <div class="people-photo">头像</div>
                </el-col>
            </el-row>
            <el-row :gutter="15">
                <el-col :span="6">
                    <el-form-item label="服务处所">
                        <el-input v-model="baseForm.servicePlace" :maxlength="25" placeholder="请填写服务处所"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="职业类别">
                        <el-cascader placeholder="请选择职业类别" :options="dic['职业类别']" 
                            filterable change-on-select clearable 
                            v-model="baseForm.occupationType" :props="{value: 'code'}"></el-cascader>
                    </el-form-item>  
                </el-col>
                <el-col :span="6">
                    <el-form-item label="职业">
                        <el-input v-model="baseForm.occupationDesc" :maxlength="12" placeholder="请填写职业"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
export default {
  props: {
    /** 是否为添加信息 */
    isAdd: {
      type: Boolean,
      default: true
    },
    /** 获取到字典表中的数据 */
    dic: {
      type: Object,
      default: {
        性别: [],
        民族: [],
        婚姻状况: [],
        政治面貌: [],
        学历: [],
        宗教信仰: [],
        职业类别: [],
        行政区划: []
      }
    },
    /** 户信息 */
    houseOptions: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      isResetDis: false, // 是否重新选择行政区
      restaurants: [],
      baseForm: {
        sex: "1", // 男性
        politicalStatus: "13", // 群众
        nation: "01", // 汉族
        religiousBelief: "00", // 无宗教信仰
        nativePlace: "610000", // 陕西省
        education: "20", // 大学本科
        marriage: "10" // 未婚
      },
      teamRules: {
        idCardNo: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          util.validate.isIDCard()
        ],
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        phone: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          util.validate.tellAndPhone()
        ]
      },
      photoUrl: "",
      sctionUrl: constants.SERVER_URL + "/fastdfs/file/upload"
    };
  },

  watch: {
    "baseForm.idCardNo"(val) {
      if (val && val.length != 18) {
        this.$emit("onBasePeople", this.baseForm.idCardNo);
      }
      if (val && val.length == 18) {
        this.baseForm.birthDate =
          val.slice(6, 10) + "-" + val.slice(10, 12) + "-" + val.slice(12, 14);
        // 性别判定
        if (val.slice(-2, -1) % 2 == 0) {
          // 女性
          this.baseForm.sex = "2";
        } else if (val.slice(-2, -1) % 2 == 1) {
          // 男性
          this.baseForm.sex = "1";
        }
      }
    }
  },
  methods: {
    handleAvatarSuccess(response) {
      this.imageUrl =
        constants.SERVER_URL + "/fastdfs/file/file/" + response.data.id;
      this.baseForm.photo = response.data.id;
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPNG = file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG && !isPNG) {
        this.$message.error("上传头像图片只能是 JPG 格式或者 PNG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return (isJPG || isPNG) && isLt2M;
    },

    querySearch(queryString, cb) {
      let restaurants = this.restaurants;
      let results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.idCardNo
            .toLowerCase()
            .indexOf(queryString.toLowerCase()) === 0
        );
      };
    },

    handleSelect(item) {
      if (item.occupationType != null) {
        let occType = [
          item.occupationType.substr(0, 1) + "0000",
          item.occupationType.substr(0, 3) + "00",
          item.occupationType
        ];
        item.occupationType = _.uniq(occType);
      }
      this.baseForm = item;
      this.$refs["baseForm"].resetFields();
      this.$refs["baseForm"].$el.querySelector("input").focus();
    }
  }
};
</script>

<style lang="scss">
.el-dialog .el-autocomplete {
  width: 100%;
}
.avatar-uploader {
  text-align: center;
  .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    &:hover {
      border-color: #409eff;
    }
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
.people-photo {
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 18px;
  background: #ddd;
  width: 180px;
  border-radius: 5px;
  display: inline-block;
}

.is-reset-dis {
  display: flex;
  .el-form-item {
    width: 100%;
    margin: 0;
  }
}
</style>