<template>
  <div class="techo-layout-container" :style="styleLayoutContainer" :class="{grayMode: grayActive}">
    <!-- 半透明遮罩 -->
    <div class="techo-layout-mask" />
    <!-- 主体内容 -->
    <div class="techo-layout-content" flex="dir:top">
      <!-- 顶栏 -->
      <div class="techo-layout-theme-header" flex-box="0" :style="styleLayoutHeader">
        <div class="techo-layout-theme-header-logo">
          <!-- <img v-if="asideCollapse" :src="`${$baseUrl}image/theme/${themeActiveSetting.name}/logo/logo.png`">
          <img v-else :src="`${$baseUrl}image/theme/${themeActiveSetting.name}/logo/logo-all.png`"> -->
          <img :src="`${$baseUrl}image/theme/${themeActiveSetting.name}/logo/logo-all.png`">
        </div>
        <!-- 顶栏右侧 -->
        <div class="techo-layout-theme-header-right">
          <!-- 如果你只想在开发环境显示这个按钮请添加 v-if="$env === 'development'" -->
          <!-- <d2-header-error-log /> -->
          <header-fullscreen />
          <!-- <header-theme /> -->
          <header-user />
        </div>
      </div>
      <!-- 下面 主页面 -->
      <div class="techo-layout-theme-container" flex-box="1" flex>
        <!-- 主体 侧边栏 -->
        <div flex-box="0" ref="aside" class="techo-layout-theme-container-aside" :style="{width: asideCollapse ? asideWidthCollapse : asideWidth}">
          <menu-side />
        </div>
        <!-- 主体 -->
        <div class="techo-layout-theme-container-main" flex-box="1" flex="dir:top">
          <div class="layout-theme-container-main-header" flex-box="0">
            <techo-tabs />
          </div>
          <div class="techo-layout-theme-container-main-body" flex-box="1" flex="dir:top">
            <transition :name="transitionActive ? 'el-fade-in-linear' : ''">
              <keep-alive>
                <router-view flex-box="1" />
              </keep-alive>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapGetters
  } from 'vuex'
  export default {
    components: {
      'menu-side': () =>
        import('./components/menu-side/menu-side.vue'),
      // 'd2-menu-header': () =>
      //     import('./components/menu-header'),
      'techo-tabs': () =>
        import('./components/tabs/tabs.vue'),
      'header-fullscreen': () =>
        import('./components/header-fullscreen/header-fullscreen.vue'),
      'header-theme': () =>
        import('./components/header-theme'),
      'header-user': () =>
        import('./components/header-user')
    },
    data() {
      return {
        // [侧边栏宽度] 正常状态
        asideWidth: '260px',
        // [侧边栏宽度] 折叠状态
        asideWidthCollapse: '65px',
      }
    },
    mounted(){
      // TODO 获取用户登录信息，判断用户是否登录
    },
    computed: {
      ...mapState('techo', {
        grayActive: state => state.gray.active,
        transitionActive: state => state.transition.active,
        asideCollapse: state => state.menu.asideCollapse
      }),
      ...mapGetters('techo', {
        themeActiveSetting: 'theme/activeSetting'
      }),
      // 最外层容器的背景图片样式
      styleLayoutContainer() {
        return {
          ...this.themeActiveSetting.backgroundImage ? {
            backgroundImage: `url('${this.$baseUrl}${this.themeActiveSetting.backgroundImage}')`
          } : {}
        }
      },
      // 页面顶部的背景图片样式
      styleLayoutHeader() {
        return {
          ...this.themeActiveSetting.headerBgImage ? {
            backgroundImage: `url('${this.$baseUrl}${this.themeActiveSetting.headerBgImage}')`
          } : {}
        }
      }
    },
    methods: {
      handleSidemenuToggle() {

      }
    }
  }
</script>

<style lang="scss">
  // 注册主题
  @import '~@/assets/style/theme/register.scss';
</style>