import mapboxgl from 'mapbox-gl'
import {
    MapboxLayer
} from '@deck.gl/mapbox';
import {
    IconLayer,
    HexagonLayer
} from '@deck.gl/layers';

// 引入MapV图层
// import {
//     MapvLayer
// } from '@supermap/iclient-mapboxgl'

class themeOper {
    /**
     * 构造函数
     * @param {*} map 操作地图对象
     */
    constructor(map) {
        this.map = map;

        this.popup = new mapboxgl.Popup({
            closeButton: false,
            closeOnClick: false
        });
    }

    /**
     * 创建聚簇图层
     * @param {*} params 
     */
    createClusterLayer(params) {
        const layer = new MapboxLayer({
            type: IconLayer,
            id: params.id,
            data: params.data,
            pickable: true,
            wrapLongitude: true,
            getPosition: d => d.coordinates,
            iconAtlas: params.img,
            onHover: (event) => {

            },
            onClick: (event) => {

            },
            sizeScale: 60
        })
    }

    /**
     * 创建离散点密度图层
     * @param {*} params 
     */
    createScatterPlotLayer(params) {
        const layer = new MapboxLayer({
            type: GeoJsonLayer,
            id: params.id,
            data: params.url,
            opacity: 0.8,
            stroked: false,
            filled: true,
            extruded: true,
            wireframe: true,
            fp64: true,
            getFillColor: [221, 221, 221, 255],
            getLineColor: [255, 255, 255],
            getLineWidth: 2,
            pickable: true,
            autoHighlight: true,
            // highlightedObjectIndex: 1000,
            highlightColor: [151, 151, 151, 255],
        })
    }

    /**
     * 创建蜂窝图
     * @param {*} params 
     */
    createHexagonLayer(params) {
        let that = this
        const layer = new MapboxLayer({
            type: HexagonLayer,
            id: params.id,
            data: params.data,
            colorRange: [
                [1, 152, 189, 200],
                [73, 227, 206, 200],
                [216, 254, 181, 200],
                [254, 237, 177, 200],
                [254, 173, 84, 200],
                [209, 55, 78, 200]
            ],
            pickable: true,
            extruded: true,
            radius: 0,
            elevationScale: 1,
            getPosition: d => [d.lon, d.lat],
            onHover: (event) => {
                if (event.object) {
                    that.popup.setLngLat(event.lngLat)
                        .setHTML(event.object.points.length)
                        .addTo(that.map);
                } else {
                    that.popup.remove();
                }

            }
        })

        this.map.addLayer(layer)
    }

    /**
     * 创建热力图
     * @param {*} params 
     */
    createHeatLayer(params) {
        // TODO 后期修改热力图渲染参数
        let layer = {
            id: params.id,
            type: "heatmap",
            source: {
                type: 'geojson',
                data: params.data
            },
            paint: {
                // "heatmap-weight": [
                //     "interpolate",
                //     ["linear"],
                //     ["get", "mag"],
                //     0, 0,
                //     6, 1
                // ],
                // "heatmap-intensity": [
                //     "interpolate",
                //     ["linear"],
                //     ["zoom"],
                //     0, 1,
                //     9, 3
                // ],
                // "heatmap-color": [
                //     "interpolate",
                //     ["linear"],
                //     ["heatmap-density"],
                //     0, "rgba(33,102,172,0)",
                //     0.2, "rgb(103,169,207)",
                //     0.4, "rgb(209,229,240)",
                //     0.6, "rgb(253,219,199)",
                //     0.8, "rgb(239,138,98)",
                //     1, "rgb(178,24,43)"
                // ],
                // "heatmap-radius": [
                //     "interpolate",
                //     ["linear"],
                //     ["zoom"],
                //     0, 2,
                //     9, 20
                // ],
                // "heatmap-opacity": [
                //     "interpolate",
                //     ["linear"],
                //     ["zoom"],
                //     7, 1,
                //     9, 0
                // ],
            }
        }

        this.map.addLayer(layer)

    }
}

export default themeOper