import screenfull from 'screenfull'

export default {
  namespaced: true,
  state: {
    // 全屏激活
    active: false
  },
  mutations: {
    /**
     * @desc 初始化监听
     */
    listen() {
      if (screenfull.enabled) {
        screenfull.on('change', () => {
          if (!screenfull.isFullscreen) {
            this.commit('techo/fullscreen/set', false)
          }
        })
      }
    },
    /**
     * @desc 切换全屏
     */
    toggle() {
      if (screenfull.isFullscreen) {
        screenfull.exit()
        this.commit('techo/fullscreen/set', false)
      } else {
        screenfull.request()
        this.commit('techo/fullscreen/set', true)
      }
    },
    /**
     * @description 设置 store 里的全屏状态
     * @param {Object} state vuex state
     * @param {Boolean} active active
     */
    set(state, active) {
      state.active = active
    }
  }
}