import {
    GeoJsonLayer
} from '@deck.gl/layers';
import {
    MapboxLayer
} from '@deck.gl/mapbox';
import {
    scaleThreshold
} from 'd3-scale';
// import DeckGL, {ScatterplotLayer} from 'deck.gl';

class layerOper {
    /**
     * 构造函数
     * @param {*} map 操作地图对象
     */
    constructor(map) {
        this.map = map;

        // 颜色范围
        this.COLOR_SCALE = scaleThreshold()
            .domain([5, 10, 15, 20, 25, 30, 40, 50, 70, 100])
            .range([
                [53, 208, 58],
                [53, 208, 135],
                [53, 202, 208],
                [237, 248, 177],
                [41, 158, 225],
                [52, 144, 239],
                [255, 201, 121],
                [255, 171, 58],
                [255, 168, 157],
                [255, 137, 121],
                [255, 98, 77],
            ]);

        // 光亮设置
        this.LIGHT_SETTINGS = {
            lightsPosition: [-125, 50.5, 5000, -122.8, 48.5, 8000],
            ambientRatio: 0.2,
            diffuseRatio: 0.5,
            specularRatio: 0.3,
            lightsStrength: [2.0, 0.0, 1.0, 0.0],
            numberOfLights: 2
        };
    }

    /**
     * 创建天地图图层
     * @param {*} params 
     */
    createTdtLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'raster',
            minzoom: 0,
            maxzoom: 22,
            isVisible: params.isVisible,
            source: {
                type: 'raster',
                tiles: [params.url],
                tileSize: 256
            }
        }
    }

    /**
     * 创建Wmts图层
     * @param {*} params 
     */
    createWmtsLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'raster',
            isVisible: params.isVisible,
            source: {
                type: 'raster',
                tiles: [params.url],
                tileSize: 256
            }
        }
    }

    /**
     * 创建Supermap发布的切片图层
     * @param {*} params 
     */
    createSuperMapZxyLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'raster',
            isVisible: params.isVisible,
            source: {
                type: 'raster',
                tiles: [params.url],
                tileSize: 256
            }
        }
    }

    /**
     * 创建WMS服务
     * @param {*} params 
     */
    createWmsLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'raster',
            layout: {
                visibility: params.isVisible ? 'visible' : 'none',
            },
            source: {
                type: 'raster',
                tiles: [params.url],
                tileSize: 256
            },
            paint: {
                "raster-opacity": 0.5, //图层显示透明度
            }
        }
    }

    /**
     * 创建GeoJson矢量面图层
     * @param {*} params 
     */
    createGeoJsonLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'fill',
            source: {
                type: 'geojson',
                data: params.url,
                generateId: true
            },
            layout: {
                visibility: params.isVisible ? 'visible' : 'none',
            },
            paint: {
                'fill-color': params.color ? params.color : "#FFF",
                'fill-outline-color': '#000',
                'fill-opacity': ["case",
                    ["boolean", ["feature-state", "hover"], false],
                    0.15,
                    0.05
                ]
            }
        }
    }

    /**
     * 创建GeoJson线状数据
     * @param {*} params 
     */
    createGeoJsonLineLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'line',
            source: {
                type: 'geojson',
                data: params.url
            },
            layout: {
                visibility: params.isVisible ? 'visible' : 'none',
            },
            paint: {
                "line-color": "#000",
                "line-width": 2
            }
        }
    }

    /**
     * 根据GeoJson服务创建标注图层
     * @param {*} params 
     */
    createGeoJsonTextLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'symbol',
            source: {
                type: 'geojson',
                generateId: true,
                data: {
                    type: "FeatureCollection",
                    features: []
                }
            },
            layout: {
                visibility: params.isVisible ? 'visible' : 'none',
                "text-field": "{NAME}",
                "text-size": params.fontSize ? params.fontSize : 20,
                "text-font": ["Microsoft YaHei Regular"],
                "text-offset": [0, 0.6],
                "text-anchor": "top"
            },
            paint: {
                "text-color": ["case",
                    ["boolean", ["feature-state", "hover"], false],
                    params.hoverColor ? params.hoverColor : "#FF0000",
                    params.color ? params.color : "#000000"
                ],
                "text-halo-color": "#FFFFFF",
                "text-halo-width": 2
            }
        }
    }

    /**
     * 创建2.5维图层
     * @param {*} params 图层参数
     * @param {*} clickFunc 点击事件回调函数
     * @param {*} hoverFunc hover事件回调函数
     */
    createExtrusionLayer(params, clickFunc, hoverFunc) {
        return new MapboxLayer({
            type: GeoJsonLayer,
            id: params.id,
            data: params.url,
            opacity: 0.8,
            stroked: false,
            filled: true,
            extruded: true,
            wireframe: true,
            fp64: true,
            getElevation: f => f.properties.HIGH ? f.properties.HIGH : 100,
            // getFillColor: [221, 221, 221, 255],
            getFillColor: f => f.properties.HIGH ? this.COLOR_SCALE(f.properties.HIGH) : [221, 221, 221, 255],
            // lightSettings: this.LIGHT_SETTINGS,
            getLineColor: [255, 255, 255, 0],
            getLineWidth: 2,
            pickable: true,
            autoHighlight: true,
            // highlightedObjectIndex: 1000,
            highlightColor: [147, 187, 206, 200],
            onClick: (event) => {
                clickFunc && clickFunc(event)
            },
            onHover: (event) => {
                hoverFunc && hoverFunc(event)
            }
        })
    }

    /**
     * 创建2.5维高亮图层
     * @param {*} params 
     */
    createExtHightLayer(params) {
        return {
            id: params.id,
            name: params.name,
            type: 'fill-extrusion',
            source: {
                type: 'geojson',
                data: params.url
            },
            paint: {
                'fill-extrusion-color': "#484896",
                'fill-extrusion-height': {
                    'property': 'HIGH',
                    'type': 'identity'
                },
                'fill-extrusion-opacity': 1
            },
            "filter": ["==", "CODE", ""]
        }
    }

    // 设置图层可见性
    setLayerVisibility(name, isVisible = false) {
        this.map.setLayoutProperty(name, 'visibility', isVisible ? 'visible' : 'none');
    }

}

export default layerOper