<template>
  <div id="app">
    <div class="page">
      <header>
        <TopBar/>
      </header>
      <main>
        <PageLeft/>
        <PageMiddle/>
        <PageRight/>
      </main>
    </div>
  </div> 
</template>

<script>
import BoxWrap from "./page/components/BoxWrap.vue";
import TopBar from "./page/components/TopBar.vue";
import PageLeft from "./page/components/PageLeft.vue";
import PageMiddle from "./page/components/PageMiddle.vue"
import PageRight from "./page/components/PageRight.vue"
import elementResizeEvent from "element-resize-event";
  export default {
    name: "App",
    components:{
      BoxWrap,
      TopBar,
      PageLeft,
      PageMiddle,
      PageRight
    },
    mounted() {
    // 设置宽度和高度变化时响应事件
    let clock
    elementResizeEvent(this.$el, () => {
      if(clock){
        clearTimeout(clock)
      }
      clock = setTimeout(() => {
        this.$bus.$emit("resizeChart");
      }, 200);
    });
  }
  };
</script>

<style lang="scss">
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*::after,
*::before{
  box-sizing: border-box;
}
.page{
  height: 100vh;
  display: flex;
  flex-direction: column;
  >main{
    flex-grow: 1;
    background: #062032;
    display: flex;
    justify-content: space-between;
    background: #000f4e;
    > .middle{
      width: 40%;
      margin:5px -8px 10px 0;
    }
    >.left{
      width: 30%;
      margin:0 5px;
    }
    >.right{
      width: 30%;
      margin:0 5px;
    }
    .left{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 5px 0 10px 0;
    }
    .right{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 5px 0 10px 0;
    }
    .middle{
      .map{
        border: 1px solid #1a3444;
        height: 100%;
      }
    }
  }
}
</style>