/**
 * 获取业务功能基础数据
 */
export default {
    namespaced: true,
    state: {
        // 行政区划
        region: []
    },
    mutations: {
        /**
         * 设置行政区域
         * @param {*} state 
         * @param {*} region 
         */
        regionSet(state, region) {
            state.region = region;
            // 持久化
            this.dispatch('techo/db/set', {
                dbName: 'sys',
                path: 'buss.region',
                value: state.region,
                user: true
            })
        },
        /**
         * 从持久化数据读取行政区域
         * @param {Object} state vuex state
         */
        async regionLoad(state) {
            // store 赋值
            state.region = await this.dispatch('techo/db/get', {
                dbName: 'sys',
                path: 'buss.region',
                defaultValue: [],
                user: true
            })
        }
    }
}