/**
 * 地图接口封装
 * @description 基于MapBoxGL和SuperMap iClient for MapboxGL的地图接口封装
 */

// 引入地图配置
import mapConfig from './config/';
// 引入地图操作
import mapOper from './mapbox/mapOper';
// 引入图层操作
import layerOper from './mapbox/layerOper';
// 引入元素操作
import markerOper from './mapbox/markerOper';
// 引入绘图操作
import drawOper from './mapbox/drawOper';
// 引入量算操作
import measureOper from './mapbox/measureOper';
// 引入专题图层操作
import themeOper from './mapbox/themeOper';

import mapboxSupport from '@mapbox/mapbox-gl-supported';

class mapApi {

    /**
     * 地图初始化
     * @param {*} el 地图容器
     * @param {*} onLoadFunc 地图加载完成回调函数
     */
    init(el, onLoadFunc) {
        let that = this
        this.$el = el;
        this.mapConfig = mapConfig

        // 检测浏览器是否支持mapboxgl
        const isSupport = mapboxSupport()
        console.log("检测浏览器是否支持mapboxgl ->> 检测结果:" + isSupport)

        // 设置地图容器Dom元素id
        mapConfig.domId = this.$el.id;

        // 地图加载回调函数
        mapConfig.onLoadFunc = function () {
            that.markerApi.initImages();
            onLoadFunc && onLoadFunc();
        }
        // 初始化地图操作
        this.mapOper = new mapOper(mapConfig, layerOper);
        // 初始化地图
        this.mapOper.initMap();
        this.map = this.mapOper.map;

        this.layerApi = new layerOper(this.map);
        // 初始化元素操作
        this.markerApi = new markerOper(this.map);
        // 初始化绘图操作
        this.drawApi = new drawOper(this.map);
        // 初始化量算操作
        this.measureApi = new measureOper(this.map);
        // 初始化专题图层操作
        this.themeApi = new themeOper(this.map);
    }

    /**
     * 清除地图多余要素
     */
    clear() {
        // 取消绘图激活
        this.drawApi.deactiveDraw();
        // 取消量算激活
        this.measureApi.deactiveMeasure();
        // 清除marker标记点
        this.markerApi.clearMarkers();
    }

    /**
     * 设置地图状态
     */
    setMapPan() {
        this.measureApi.deactiveMeasure();
    }
}


export default mapApi;