<template>
    <div id="myChartOne" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
export default {
  data() {
    return {
      lineOptions: {},
      lineChart: null
    };
  },
  mounted() {
    this.drawLine();
    this.lineChart = this.$echarts.init(this.$el);
    this.lineChart.setOption(this.lineOptions);
    this.$bus.$on("resizeChart", () => {
      this.lineChart.resize();
    });
    // let that = this;
    // setTimeout(() => {
    //   let app = {
    //     currentIndex: -1
    //   };
    //   setInterval(function() {
    //     let dataLength = 6;
    //     // 取消之前高亮的图形
    //     that.lineChart.dispatchAction({
    //       type: "downplay",
    //       seriesIndex: 0,
    //       dataIndex: app.currentIndex
    //     });
    //     app.currentIndex = (app.currentIndex + 1) % dataLength;
    //     // 高亮当前图形
    //     that.lineChart.dispatchAction({
    //       type: "showTip",
    //       seriesIndex: 0,
    //       dataIndex: app.currentIndex
    //     });
    //   }, 1000);
    // }, 1000);
  },
  methods: {
    drawLine() {
      var myChart = this.$echarts.init(document.getElementById("myChartOne"));
      var category = [
        "餐饮场所消防",
        "视频监控点",
        "环境整治",
        "已办事项",
        "大型企业",
        "常住人口"
      ];
      var barData = [10, 20, 30, 31, 25, 36];
      var option = {
        tooltip: {
          left:'center',
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          left: "20%",
          right: "4%",
          bottom: "4",
          top: "3%"
        },
        xAxis: {
          type: "value",
          splitLine: { show: false },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          type: "category",
          data: category,
          
          splitLine: { show: false },
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false
          },
          axisLabel:{
            color: "#FFFFFF",
            fontSize: 12,
            margin:75,
            textStyle:{
              align:'left',
            }
          },
          offset: 10,
          
        },
        series: [
          {
            name: "访问量",
            type: "bar",
            data: barData,
            barWidth: 14,
            barGap: 10,
            smooth: true,
            itemStyle: {
                normal: {
                  color: "#42A7FF"
                }
              },
            label: {
              normal: {
                show: true,
                position: "right",
                offset: [5, -2],
                textStyle: {
                  color: "#FFFFFF",
                  fontSize: 13,
                  fontFamily: "Microsoft YaHei",
                 
                }
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  }
};
</script>

