// 设置文件
import setting from '@/setting'

export default {
  namespaced: true,
  state: {
    // 可以在多页 tab 模式下显示的页面
    pool: [],
    // 当前显示的多页面列表
    opened: setting.page.opened,
    actived: setting.page.actived,
    // 当前显示的固定多页面列表
    fixed: setting.page.fixed,
    // 当前页面
    current: 'show'
  },
  // getters: {
  //   /**
  //    * @description 从当前所有打开的多标签页里返回需要缓存的页面 name
  //    * @param {*} state vuex state
  //    */
  //   keepAlive(state) {
  //     return state.opened.filter((item) => {
  //       if (item.meta) {
  //         if (item.meta.notCache) {
  //           return false
  //         }
  //       }
  //       return true
  //     }).map(e => e.name)
  //   }
  // },
  mutations: {
    /**
     * @class current
     * @description 打开一个新的页面
     * @param {Object} state vuex state
     * @param {Object} param { name, params, query } 路由信息
     */
    open(state, {
      name,
      params,
      query
    }) {
      // 已经打开的页面
      const opened = state.opened
      // 判断此页面是否已经打开 并且记录位置
      let pageOpendIndex = 0
      const pageOpend = opened.find((page, index) => {
        const same = page.name === name
        pageOpendIndex = same ? index : pageOpendIndex
        return same
      })
      if (pageOpend) {
        // 页面以前打开过 但是新的页面可能 name 一样，参数不一样
        this.commit('techo/page/openedUpdate', {
          index: pageOpendIndex,
          params,
          query
        })
      } else {
        // 页面以前没有打开过
        const page = state.pool.find(t => t.name === name)
        if (page) {
          this.commit('techo/page/add', {
            tag: page,
            params,
            query
          })
        }
      }
      this.commit('techo/page/currentSet', name)
    },
    /**
     * @class active
     * @description 设置激活菜单
     * @param {*} state 
     * @param {*} param
     */
    active(state, {
      name,
      params,
      query
    }) {
      // 查找页面
      const page = state.pool.find(t => t.name === name)

      // 设置当前激活的菜单
      if (page && page.path !== "map") {
        // 设置新的 tag 在新打开一个以前没打开过的页面时使用
        const newTag = page
        newTag.params = params || newTag.params
        newTag.query = query || newTag.query
        // 添加进当前显示的页面数组
        state.actived = newTag
      }

      // 设置当前激活的标签页
      if (page && page.meta.isFirst) {
        this.commit('techo/page/currentSet', 'show')

        this.commit('techo/page/fixedSet', [{
          name: 'show',
          meta: {
            title: '首页',
            icon: 'industry',
            requiresAuth: false
          }
        }])
      } else if (page && page.meta.dataManage) {
        // 数据管理
        this.commit('techo/page/fixedSet', [{
            name: 'show',
            meta: {
                title: page.meta.title,
                icon: 'th-list',
                requiresAuth: false
            }
          }])
      } else {
        this.commit('techo/page/fixedSet', setting.page.fixed)
      }

      if (page && page.path == "map") {
        this.commit('techo/page/currentSet', 'map')
      } else if (page && page.meta.menu) {
        this.commit('techo/page/currentSet', 'list')
      } else if (page && page.meta.menu == undefined) {
        this.commit('techo/page/currentSet', 'show')
      }

      // 持久化
      this.commit('techo/page/actived2db')
    },
    /**
     * @class current
     * @description 设置当前激活的标签页 name
     * @param {Object} state vuex state
     * @param {String} name new name
     */
    currentSet(state, name) {
      state.current = name
    },
    fixedSet(state, list) {
      state.fixed = list
    },
    /**
     * 将 opened 属性赋值并持久化 在这之前请先确保已经更新了 state.opened
     * @param {Object} state vuex state
     */
    actived2db(state) {
      this.dispatch('techo/db/set', {
        dbName: 'sys',
        path: 'page.actived',
        value: state.opened,
        user: true
      })
    },
    /**
     * @class opened
     * @description 更新页面列表上的某一项
     * @param {Object} state vuex state
     * @param {Object} param { index, params, query } 路由信息
     */
    openedUpdate(state, {
      index,
      params,
      query
    }) {
      // 更新页面列表某一项
      const page = state.opened[index]
      page.params = params || page.params
      page.query = query || page.query
      state.opened.splice(index, 1, page)
      // 持久化
      this.commit('techo/page/opend2db')
    },
    /**
     * 将 opened 属性赋值并持久化 在这之前请先确保已经更新了 state.opened
     * @param {Object} state vuex state
     */
    opend2db(state) {
      this.dispatch('techo/db/set', {
        dbName: 'sys',
        path: 'page.opened',
        value: state.opened,
        user: true
      })
    },
    /**
     * @class opened
     * @description 从持久化数据载入分页列表
     * @param {Object} state vuex state
     */
    async openedLoad(state) {
      // store 赋值
      const value = await this.dispatch('techo/db/get', {
        dbName: 'sys',
        path: 'page.opened',
        defaultValue: setting.page.opened,
        user: true
      })
      // 在处理函数中进行数据优化 过滤掉现在已经失效的页签或者已经改变了信息的页签
      // 以 name 字段为准
      // 如果页面过多的话可能需要优化算法
      // valid 有效列表 1, 1, 0, 1 => 有效, 有效, 失效, 有效
      const valid = []
      // 处理数据
      state.opened = value.map((opened) => {
        // 忽略首页
        if (opened.name === 'index') {
          valid.push(1)
          return opened
        }
        // 尝试在所有的支持多标签页的页面里找到 name 匹配的页面
        const find = state.pool.find(item => item.name === opened.name)
        // 记录有效或无效信息
        valid.push(find ? 1 : 0)
        // 返回合并后的数据 新的覆盖旧的
        // 新的数据中一般不会携带 params 和 query, 所以旧的参数会留存
        return Object.assign({}, opened, find)
      }).filter((opened, index) => valid[index] === 1)
    },
    /**
     * @class opened
     * @description 新增一个 tag (打开一个页面)
     * @param {Object} state vuex state
     * @param {Object} param new tag info
     */
    add(state, {
      tag,
      params,
      query
    }) {
      // 设置新的 tag 在新打开一个以前没打开过的页面时使用
      const newTag = tag
      newTag.params = params || newTag.params
      newTag.query = query || newTag.query
      // 添加进当前显示的页面数组
      state.opened.push(newTag)
      // 持久化
      this.commit('techo/page/opend2db')
    },
    /**
     * @class opened
     * @description 关闭一个 tag (关闭一个页面)
     * @param {Object} state vuex state
     * @param {Object} param { tagName: 要关闭的标签名字, vm: vue }
     */
    close(state, {
      tagName,
      vm
    }) {
      // 下个新的页面
      let newPage = state.opened[0]
      const isCurrent = state.current === tagName
      // 如果关闭的页面就是当前显示的页面
      if (isCurrent) {
        // 去找一个新的页面
        const len = state.opened.length
        for (let i = 1; i < len; i++) {
          if (state.opened[i].name === tagName) {
            if (i < len - 1) {
              newPage = state.opened[i + 1]
            } else {
              newPage = state.opened[i - 1]
            }
            break
          }
        }
      }
      // 找到这个页面在已经打开的数据里是第几个
      const index = state.opened.findIndex(page => page.name === tagName)
      if (index >= 0) {
        state.opened.splice(index, 1)
      }
      // 持久化
      this.commit('techo/page/opend2db')
      // 最后需要判断是否需要跳到首页
      if (isCurrent) {
        const {
          name = '', params = {}, query = {}
        } = newPage
        const routerObj = {
          name,
          params,
          query
        }
        vm.$router.push(routerObj)
      }
    },
    /**
     * @class opened
     * @description 关闭当前标签左边的标签
     * @param {Object} state vuex state
     * @param {Object} param { pageSelect: 当前选中的tagName, vm: vue }
     */
    closeLeft(state, {
      pageSelect,
      vm
    } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.name === pageAim) {
          currentIndex = index
        }
      })
      if (currentIndex > 0) {
        state.opened.splice(1, currentIndex - 1)
      }
      state.current = pageAim
      if (vm && vm.$route.name !== pageAim) {
        vm.$router.push({
          name: pageAim
        })
      }
      // 持久化
      this.commit('techo/page/opend2db')
    },
    /**
     * @class opened
     * @description 关闭当前标签右边的标签
     * @param {Object} state vuex state
     * @param {Object} param { pageSelect: 当前选中的tagName, vm: vue }
     */
    closeRight(state, {
      pageSelect,
      vm
    } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.name === pageAim) {
          currentIndex = index
        }
      })
      state.opened.splice(currentIndex + 1)
      state.current = pageAim
      if (vm && vm.$route.name !== pageAim) {
        vm.$router.push({
          name: pageAim
        })
      }
      // 持久化
      this.commit('techo/page/opend2db')
    },
    /**
     * @class opened
     * @description 关闭当前激活之外的 tag
     * @param {Object} state vuex state
     * @param {Object} param { pageSelect: 当前选中的tagName, vm: vue }
     */
    closeOther(state, {
      pageSelect,
      vm
    } = {}) {
      const pageAim = pageSelect || state.current
      let currentIndex = 0
      state.opened.forEach((page, index) => {
        if (page.name === pageAim) {
          currentIndex = index
        }
      })
      if (currentIndex === 0) {
        state.opened.splice(1)
      } else {
        state.opened.splice(currentIndex + 1)
        state.opened.splice(1, currentIndex - 1)
      }
      state.current = pageAim
      if (vm && vm.$route.name !== pageAim) {
        vm.$router.push({
          name: pageAim
        })
      }
      // 持久化
      this.commit('techo/page/opend2db')
    },
    /**
     * @class opened
     * @description 关闭所有 tag
     * @param {Object} state vuex state
     * @param {Object} vm vue
     */
    closeAll(state, vm) {
      state.opened.splice(1)
      // 持久化
      this.commit('techo/page/opend2db')
      // 关闭所有的标签页后需要判断一次现在是不是在首页
      if (vm.$route.name !== 'index') {
        vm.$router.push({
          name: 'index'
        })
      }
    },
    /**
     * @class pool
     * @description 保存 pool (候选池)
     * @param {Object} state vuex state
     * @param {Array} routes routes
     */
    init(state, routes) {
      const pool = []
      const push = function (routes) {
        routes.forEach((route) => {
          if (route.children) {
            push(route.children)
          } else {
            const {
              meta,
              name,
              path
            } = route
            pool.push({
              meta,
              name,
              path
            })
          }
        })
      }
      push(routes)
      state.pool = pool
    }
  }
}